import request from '@/utils/request'

export function loadlistUser () {
  return request({
    url: '/app/user/list',
    method: 'get'
  })
}

export function loadUser (id) {
  return request({
    url: '/app/user/get/' + id,
    method: 'get'
  })
}

export function editUser (id, data) {
  return request({
    url: '/app/user/edit/' + id,
    method: 'post',
    data: data
  })
}

export function addUser (data) {
  return request({
    url: '/app/user/add',
    method: 'post',
    data: data
  })
}

export function getWarehouse (type = '') {
  let q
  if (type === '') {
    q = '/app/user/warehouse'
  } else {
    q = '/app/user/warehouse?type=all'
  }
  return request({
    url: q,
    method: 'get'
  })
}



