<template>
<div class="row">
    <div class="col-md-12">
      <div class="card" v-loading="isLoading">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.spend.list') }}</h4>
        </div>
        <div class="card-content">
          <el-table :data="listData.data">
            <el-table-column type="index" min-width="70" width="70">

            </el-table-column>
            <el-table-column :label="$t('app.spend.price')">
              <template slot-scope="props">
                <span>{{ $price(props.row.price) + $t('app.price_suffix') }}</span>
              </template>
            </el-table-column>
            <el-table-column :label="$t('app.spend.description')" prop="description">
            </el-table-column>
            <el-table-column :label="$t('app.user.name')" prop="user.name">
            </el-table-column>

            <el-table-column :label="$t('app.bill.date_in')">
              <template slot-scope="props">
                <span>{{ props.row.created_at | moment('H:mm DD/MM/YYYY') }}</span>
              </template>
            </el-table-column>
            <el-table-column :label="$t('app.action')">
              <template slot-scope="props">
                <template v-if="canModify">
                  <a class="btn btn-danger btn-icon btn-fill btn-square" @click="deleteSpend(props.row.id)"><i class="fa fa-times"></i></a>
                </template>
              </template>
            </el-table-column>
          </el-table>
          <div class="u-cus-pagination">
            <el-pagination v-if="listData.total > listData.per_page"
              background
              layout="prev, pager, next"
              :total="listData.total"
              :page-size="listData.per_page"
              :current-page="currentPage"
              @current-change="currentChange">
            </el-pagination>
          </div>
      </div>
    </div>
</div>
</div>
</template>

<script>
  import swal from 'sweetalert'
  import {getListSpend, deleteSpend} from '@/api/spend'
  export default {
    data () {
      return {
        isLoading: false,
        listData: {},
        canModify: false
      }
    },
    methods: {
      loadListSpend (index = 0) {
        this.isLoading = true
        getListSpend(index).then(e => {
          if (e.data.is_valid) {
            this.listData = e.data.data
            if (e.data.is_modify) {
              this.canModify = true
            }
          }
          this.isLoading = false
        })
      },
      deleteSpend (id) {
        swal({
          text: this.$t('app.spend.delete'),
          icon: 'warning',
          buttons: true,
          dangerMode: true
        }).then(isOK => {
          if (isOK) {
            this.isLoading = true
            deleteSpend(id).then(e => {
              this.isLoading = false
              if (e.data.status === 'success') {
                this.loadListSpend(this.currentPage)
              }
            })
          }
        })
      }
    },
    mounted () {
      this.loadListSpend()
    }
  }
</script>


<style>
  .u-cus-pagination li.more {
      float: unset !important;
      z-index: unset !important;
      display: inline !important;
      padding: 0 !important;
  }
</style>



