<template>
  <div class="row">
    <div class="col-md-12 col-lg-6">
      <div class="card">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.permission.new') }}</h4>
        </div>
        <div class="card-content" v-loading="isLoading">
          <form @submit.prevent="createPermission">
            <div class="form-group">
              <label>{{ $t('app.permission.name') }}</label>
              <input type="text" :placeholder="$t('app.permission.name')" name="name" class="form-control" required>
            </div>
            <div class="float-right">
              <button type="submit" class="btn btn-square btn-fill btn-wd btn-success">{{ $t('app.save') }}</button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {createPermission} from '@/api/permission'
  export default {
    data () {
      return {
        isLoading: false
      }
    },
    methods: {
      createPermission (e) {
        let formData = new FormData(e.target)
        let form = e.target
        this.isLoading = true
        createPermission(formData).then(e => {
          if (e.data.status === 'success') {
            form.reset()
          }
          this.isLoading = false
        })
      }
    }
  }
</script>



