<template>
<div class="row">
  <div class="col-lg-6 col-md-12">
    <div class="card">
      <div class="card-header" v-if="'name' in customer">
        <h4 class="card-title">{{ customer.name }}</h4>
      </div>
      <div class="card-content">
        <div class="row" v-loading="isLoading">
          <div class="col-md-12">
            <form @submit.prevent="submitCustomer">
              <div class="form-group">
                <label>{{ $t('app.customer.name') }}</label>
                <input type="text" :placeholder="$t('app.customer.name')" name="name" v-model="customer.name" class="form-control" required>
              </div>
              <div class="form-group">
                <label>{{ $t('app.customer.phone') }}</label>
                <input type="text" :placeholder="$t('app.customer.phone')" name="phone" v-model="customer.phone" class="form-control" required>
              </div>
              <div class="form-group">
                <label>{{ $t('app.customer.address') }}</label>
                <input type="text" :placeholder="$t('app.customer.address')" name="address" v-model="customer.address" class="form-control">
              </div>
              <div class="form-group">
                <label>{{ $t('app.customer.zalo') }}</label>
                <input type="text" :placeholder="$t('app.customer.zalo')" name="zalo" v-model="customer.zalo" class="form-control">
              </div>
              <div class="form-group">
                <label>{{ $t('app.customer.facebook') }}</label>
                <input type="text" :placeholder="$t('app.customer.facebook')" name="facebook" v-model="customer.facebook" class="form-control">
              </div>
              <div class="float-right">
                <button type="submit" class="btn btn-fill btn-sm btn-success">{{ $t('app.save') }}</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
  import {getCustomer, editCustomer} from '@/api/customer'
  export default {
    data () {
      return {
        isLoading: false,
        customer: {}
      }
    },
    methods: {
      submitCustomer (e) {
        this.isLoading = true
        let id = this.customer.id
        editCustomer(id, this.customer).then(e => {
          this.isLoading = false
        })
      }
    },
    mounted () {
      let id = this.$route.params.id
      this.isLoading = true
      getCustomer(id).then(e => {
        if (e.data.is_valid) {
          this.customer = e.data.customer
        }
        this.isLoading = false
      })
    }
  }
</script>



