import request from '@/utils/request'

export function createProductType (data) {
  return request({
    url: '/product-type',
    method: 'post',
    data: data
  })
}

export function searchProductType (name) {
  return request({
    url: '/app/product-type/search?name=' + name,
    method: 'get'
  })
}

export function getListProductType (index) {
  return request({
    url: '/product-type?page=' + index,
    method: 'get'
  })
}

export function getProductType (id) {
  return request({
    url: '/product-type/' + id,
    method: 'get'
  })
}

export function editProductType (id, data) {
  return request({
    url: '/product-type/' + id,
    method: 'put',
    data: data
  })
}

export function deleteProductType (id) {
  return request({
    url: '/product-type/' + id,
    method: 'delete'
  })
}

export function getAllProductType () {
  return request({
    url: '/app/product-type/get-all',
    method: 'get'
  })
}



