import request from '@/utils/request'

export function loadComponents (form) {
  return request({
    url: '/app/load-components',
    method: 'get'
  })
}

export function saleSaveBillRetail (id, data, customer, saleOf, priceOf, note) {
  return request({
    url: '/app/sale/retail',
    method: 'post',
    data: {
      body: data,
      id: id,
      customer_id: customer,
      sale_of: saleOf,
      price_of: priceOf,
      note: note
    }
  })
}



