<template>
  <nav class="navbar navbar-default">
    <div class="container-fluid">
      <div class="navbar-minimize">
        <button class="btn btn-fill btn-icon" @click="minimizeSidebar">
          <i :class="$sidebar.isMinimized ? 'fa fa-ellipsis-v' : 'fa fa-bars'"></i>
        </button>
      </div>
      <div class="navbar-header">
        <button type="button" class="navbar-toggle" :class="{toggled: $sidebar.showSidebar}" @click="toggleSidebar">
          <span class="sr-only">Toggle navigation</span>
          <span class="icon-bar bar1"></span>
          <span class="icon-bar bar2"></span>
          <span class="icon-bar bar3"></span>
        </button>
        <a class="navbar-brand">{{ $t('app.routes.' + this.$route.name) }}</a>
      </div>
      <div class="collapse navbar-collapse">
        <div class="u-custom-select-warehouse" v-if="warehouses.length >= 1">
          <el-select v-model="selected" @change="changeSelectedWarhouse">
            <el-option
                v-for="item in warehouses"
                :key="item.id"
                :label="item.alias"
                :value="item.id">
            </el-option>
          </el-select>
        </div>
        <ul class="nav navbar-nav navbar-right" v-if="user">
          <li>
            <p>{{ $t('app.welcome') }}</p>
          </li>
          <drop-down tag="li" :title="user.name" icon="fa fa-user-o">
            <li>
              <a href="#!" @click.prevent="logout">{{ $t('app.topnav.logout') }} <i class="fa fa-sign-out"></i> </a>
            </li>
          </drop-down>

        </ul>
      </div>
    </div>
  </nav>
</template>
<script>
  export default {
    data () {
      return {
        activeNotifications: false,
        user: this.$store.getters.user,
        selected: null
      }
    },
    methods: {
      capitalizeFirstLetter (string) {
        return string.charAt(0).toUpperCase() + string.slice(1)
      },
      toggleNotificationDropDown () {
        this.activeNotifications = !this.activeNotifications
      },
      closeDropDown () {
        this.activeNotifications = false
      },
      toggleSidebar () {
        this.$sidebar.displaySidebar(!this.$sidebar.showSidebar)
      },
      hideSidebar () {
        this.$sidebar.displaySidebar(false)
      },
      minimizeSidebar () {
        this.$sidebar.toggleMinimize()
      },
      logout () {
        this.$store.dispatch('changeLoading', true)
        this.$store.dispatch('logout').then((e) => {
          if (e.data.status === 'success') {
            this.$router.push({
              path: '/login'
            })
          }
          this.$store.dispatch('changeLoading', false)
        })
      },
      changeSelectedWarhouse (e) {
        this.$store.dispatch('changeSelectedWarehouse', e)
        this.$store.dispatch('changeReRender')
      }
    },
    computed: {
      warehouses: function () {
        return this.$store.getters.warehouses
      },
      selectedWarehouse: function () {
        return this.$store.getters.selectedWarehouse
      }
    },
    watch: {
      'selectedWarehouse': function () {
        this.selected = this.selectedWarehouse
      }
    },
    mounted () {
      this.selected = this.$store.getters.selectedWarehouse
    }
  }

</script>
<style scoped>
.navbar .navbar-nav > li > p {
  line-height: 1.42857;
  margin: 16px 0px;
  padding: 10px 15px;
}
.btn.btn-icon, .navbar .navbar-nav > li > a.btn.btn-icon {
  border-radius: unset !important;
}
.u-custom-select-warehouse {
  padding: 18px;
  display: inline-block;
}
</style>



