import store from '@/store'
export default (to, from, next) => {
  let requiredAuth = to.matched.some(re => re.meta.requiredAuth)
  let token = store.getters.token
  // check if listShower then show select type in Content.vue
  let listShower = store.getters.listShower
  if (listShower.includes(to.name)) {
    store.dispatch('changeValidTypeProduct', true)
  } else {
    store.dispatch('changeValidTypeProduct', false)
  }
  // Check permission
  if (requiredAuth && !token) {
    next('/login')
  } else if (to.path === '/login' && token) {
    next('/admin')
  } else {
    next()
  }
}



