<template>
<div class="row">
  <div class="col-lg-6 col-md-12">
    <div class="card">
      <div class="card-header" v-if="'name' in warehouse">
        <h4 class="card-title">{{ warehouse.name === 'root' ? $t('app.supplier.root') : warehouse.name }}</h4>
      </div>
      <div class="card-content">
        <div class="row" v-loading="isLoading">
          <div class="col-md-12">
            <form @submit.prevent="submitWarehouse">
              <div class="form-group">
                <label>{{ $t('app.warehouse.name') }}</label>
                <input type="text" :placeholder="$t('app.warehouse.name')" name="name" v-model="warehouse.name" class="form-control" required>
              </div>
              <div class="form-group">
                <label>{{ $t('app.warehouse.title') }}</label>
                <input type="text" :placeholder="$t('app.warehouse.title')" name="title" v-model="warehouse.title" class="form-control">
                <small>{{ $t('app.warehouse.sub_title') }}</small>
              </div>
              <div class="form-group">
                <label>{{ $t('app.warehouse.address') }}</label>
                <input type="text" :placeholder="$t('app.warehouse.address')" name="address" v-model="warehouse.address" class="form-control">
              </div>
              <div class="form-group">
                <label>{{ $t('app.warehouse.phone') }}</label>
                <input type="text" :placeholder="$t('app.warehouse.phone')" name="phone" v-model="warehouse.phone" class="form-control">
              </div>
              <div class="form-group">
                <label>{{ $t('app.description') }}</label>
                <textarea name="description" class="form-control" rows="4" v-model="warehouse.description"></textarea>
              </div>
              <div class="form-group">
                <!-- <label>{{ $t('app.status') }}</label>
                <br>
                <p-switch v-model="switches.isActiveWarehouse">
                  <i class="fa fa-check" slot="on"></i>
                  <i class="fa fa-close" slot="off"></i>
                </p-switch> -->
              </div>
              <div class="float-right">
                <!-- HIDDEN -->
                <button type="submit" class="btn btn-fill btn-sm btn-success">{{ $t('app.save') }}</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
  import {getWarehouse, editWarehouse} from '@/api/warehouse'
  export default {
    data () {
      return {
        isLoading: false,
        warehouse: {}
      }
    },
    methods: {
      submitWarehouse (e) {
        this.isLoading = true
        let id = this.warehouse.id
        editWarehouse(id, this.warehouse).then(e => {
          this.isLoading = false
        })
      },
      getWarehouse (id) {
        this.isLoading = true
        getWarehouse(id).then(e => {
          if (e.data.is_valid) {
            this.warehouse = e.data.warehouse
          }
          this.isLoading = false
        })
      }
    },
    mounted () {
      this.getWarehouse(this.$route.params.id)
    }
  }
</script>



