<template>
<div class="row">
  <div class="col-lg-6 col-md-12">
    <div class="card">
      <div class="card-header">
        <h4 class="card-title">{{ $t('app.sale_of.add') }}</h4>
      </div>
      <div class="card-content">
        <div class="row" v-loading="isLoading">
          <div class="col-md-12">
            <form @submit.prevent="submit">
              <div class="form-group">
                <label>{{ $t('app.sale_of.name') }}</label>
                <input type="text" :placeholder="$t('app.sale_of.name')" name="name" class="form-control" required>
              </div>
              <div class="form-group">
                <label>{{ $t('app.sale_of.percent') }}</label>
                <input type="number" :placeholder="$t('app.sale_of.percent')" name="percent" class="form-control" required>
              </div>
              <div class="form-group">
                <label>{{ $t('app.sale_of.type') }}</label>
                <select name="type" class="form-control" v-model="selectedType">
                  <template v-for="type in types">
                    <option :value="type.value">{{ type.name }}</option>
                  </template>
                </select>
              </div>
              <div class="form-group" v-if="selectedType === 'for_product_type'">
                <label>{{ $t('app.sale_of.product_type') }}</label>
                <select name="product_type_id" required class="form-control">
                  <template v-for="productType in productTypes">
                    <option :value="productType.id">{{ productType.name }}</option>
                  </template>
                </select>
              </div>
              <div class="float-right">
                <button type="submit" class="btn btn-fill btn-sm btn-success">{{ $t('app.save') }}</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
  import {getAllProductType} from '@/api/product-type'
  import {createSaleOf} from '@/api/saleof'
  export default {
    data () {
      return {
        isLoading: false,
        types: [
          {
            name: this.$t('app.sale_of.for_all'),
            value: 'for_all'
          },
          {
            name: this.$t('app.sale_of.for_product_type'),
            value: 'for_product_type'
          }
        ],
        selectedType: 'for_all',
        productTypes: []
      }
    },
    methods: {
      submit (e) {
        this.isLoading = true
        let formData = new FormData(e.target)
        let form = e.target
        createSaleOf(formData).then(e => {
          if (e.data.status === 'success') {
            this.selectedType = 'for_all' // reset type select if success
            form.reset()
          }
          this.isLoading = false
        })
      },
      getAllProductType () {
        this.isLoading = false
        getAllProductType().then(e => {
          if (e.data.is_valid) {
            this.productTypes = e.data.product_type
          }
          this.isLoading = false
        })
      }
    },
    mounted () {
      this.getAllProductType()
    }
  }
</script>



