import {loadComponents} from '@/api/app'
import {getWarehouse, setWarehouse} from '@/utils/auth'
import i18n from '@/lang'
export default {
  state: {
    message: 'Hello',
    host: 'https://ban-hang-uchinka.c9users.io:8081/api',
    locale: 'vi',
    isLoading: false,
    isDisable: false,
    warehouses: [],
    reRender: 1,
    selectedWarehouse: getWarehouse(),
    isValidSelectTypeProduct: false,
    valueSelectTypeProduct: 'warehouse',
    isRootWarehouse: false,
    listShower: ['product', 'detail_product', 'bill_out']
  },
  mutations: {
    SET_LOADING: (state, value) => {
      state.isLoading = value
    },
    SET_DISABLE: (state, value) => {
      state.isLoading = value
    },
    SET_WAREHOUSE: (state, value) => {
      state.warehouses = value.warehouse.map(e => {
        e.alias = e.name
        if (e.name === 'root') {
          e.alias = i18n.t('app.warehouse.root')
        }
        return e
      })
      if (value.warehouse.length >= 1 && getWarehouse() === null) {
        state.selectedWarehouse = value.warehouse[0].id
        setWarehouse(value.warehouse[0].id)
      }
      state.isRootWarehouse = value.is_root_warehouse
    },
    SET_RERENDER: (state, value) => {
      state.reRender += 1
    },
    SET_SELECTED_WAREHOUSE: (state, value) => {
      state.selectedWarehouse = value
      setWarehouse(value)
    },
    SET_TYPE_PRODUCT: (state, value) => {
      state.isValidSelectTypeProduct = value
    },
    SET_VALUE_TYPE_PRODUCT: (state, value) => {
      state.valueSelectTypeProduct = value
    }
  },
  actions: {
    changeLoading ({commit}, value) {
      commit('SET_LOADING', value)
    },
    changeDisable ({commit}, value) {
      commit('SET_DISABLE', value)
    },
    loadComponents ({commit}, value) {
      return new Promise((resolve, reject) => {
        loadComponents().then(e => {
          if (e.data.is_valid) {
            commit('SET_WAREHOUSE', e.data)
          }
          resolve(e)
        }).catch(err => reject(err))
      })
    },
    changeReRender ({commit}, value) {
      commit('SET_RERENDER', value)
    },
    changeSelectedWarehouse ({commit}, value) {
      commit('SET_SELECTED_WAREHOUSE', value)
    },
    changeValidTypeProduct ({commit}, value) {
      commit('SET_TYPE_PRODUCT', value)
    },
    changeValueTypeSelect ({commit}, value) {
      commit('SET_VALUE_TYPE_PRODUCT', value)
    }
  }
}



