<template>
  <div class="col-lg-6 col-md-12">
    <div class="card" v-loading="isLoading">
      <div class="card-header">
        <h4 class="card-title">{{ productType.name }}</h4>
      </div>
      <div class="card-content">
        <div class="row">
          <div class="col-md-12">
            <form @submit.prevent="submit">
              <div class="form-group">
                <label>{{ $t('app.product_type.name') }}</label>
                <input type="text" :placeholder="$t('app.product_type.name')" name="name" :value="productType.name" class="form-control" required>
              </div>
              <div class="form-group">
                <label>{{ $t('app.description') }}</label>
                <textarea name="description" class="form-control" rows="4" v-model="productType.description"></textarea>
              </div>
              <div class="float-right">
                <!-- HIDDEN -->
                <button type="submit" class="btn btn-fill btn-sm btn-success">{{ $t('app.save') }}</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {getProductType, editProductType} from '@/api/product-type'
  export default {
    data () {
      return {
        productType: {},
        isLoading: false
      }
    },
    mounted () {
      let id = this.$route.params.id
      this.isLoading = true
      getProductType(id).then(e => {
        if (e.data.is_valid) {
          this.productType = e.data.product_type
        }
        this.isLoading = false
      })
    },
    methods: {
      submit (e) {
        this.isLoading = true
        let id = this.productType.id
        editProductType(id, this.productType).then(e => {
          this.isLoading = false
        })
      }
    }
  }
</script>



