import request from '@/utils/request'

export function createSpend (data) {
  return request({
    url: '/spend/',
    method: 'post',
    data: data
  })
}

export function getListSpend (index) {
  return request({
    url: '/spend?page=' + index,
    method: 'get'
  })
}

export function deleteSpend (id) {
  return request({
    url: '/spend/' + id,
    method: 'delete'
  })
}



