import request from '@/utils/request'

export function createSupplier (data) {
  return request({
    url: '/supplier',
    method: 'post',
    data: data
  })
}

export function editSupplier (data, id) {
  return request({
    url: '/supplier/' + id,
    method: 'put',
    data: data
  })
}

export function loadSupplier (paginate = null) {
  let query = '/supplier'
  if (paginate) {
    query = '/supplier?page=' + paginate
  }
  return request({
    url: query,
    method: 'get'
  })
}

export function changeStatusSupplier (id) {
  return request({
    url: '/supplier/change-status/' + id,
    method: 'post'
  })
}

export function deleteSupplier (id) {
  return request({
    url: '/supplier/change-status/' + id,
    method: 'post'
  })
}


export function getSupplier (id) {
  return request({
    url: '/supplier/' + id,
    method: 'get'
  })
}

export function searchSupplier (name) {
  return request({
    url: '/app/supplier/search?name=' + name,
    method: 'get'
  })
}



