<template>
  <div class="row">
    <div class="col-md-12 col-lg-6">
      <div class="card" v-loading="isLoading">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.routes.permission') }}</h4>
        </div>
        <div class="card-content">
          <div class="float-right">
            <button class="btn btn-primary btn-fill btn-icon btn-square" @click="newPermission"><i class="fa fa-plus"></i> {{ $t('app.permission.new') }}</button>
          </div>
          <h4 style="font-weight: 400;">{{ $t('app.permission.list') }}</h4>
          <el-table :data="listPermission">
            <el-table-column type="index">

            </el-table-column>
            <el-table-column :label="$t('app.permission.name')" prop="name" min-width="100">

            </el-table-column>
            <el-table-column :label="$t('app.count')" prop="user_count" min-width="100">

            </el-table-column>
            <el-table-column :label="$t('app.action')" min-width="100">
              <template slot-scope="props">
                <button class="btn btn-sm btn-warning btn-icon btn-fill btn-square" @click.prevent="handleEdit(props.row, props.$index)"><i class="fa fa-pencil-square-o"></i></button>
                <button class="btn btn-sm btn-danger btn-icon btn-fill btn-square" @click.prevent="handleDelete(props.row, props.$index)"><i class="fa fa-times"></i></button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {loadListPermission} from '@/api/permission'
  export default {
    data () {
      return {
        listPermission: [],
        isLoading: false
      }
    },
    methods: {
      loadListPermission () {
        this.isLoading = true
        loadListPermission().then(e => {
          if (e.data.is_valid) {
            this.listPermission = e.data.data.filter(e => {
              return e.super_admin !== 1
            })
          }
          this.isLoading = false
        })
      },
      handleEdit (row, index) {
        this.$router.push({
          name: 'edit_permission',
          params: {
            id: row.id
          }
        })
      },
      newPermission () {
        this.$router.push({
          name: 'add_permission'
        })
      }
    },
    mounted () {
      this.loadListPermission()
    }
  }
</script>



