<template>
  <div class="row">
    <div class="col-md-12">
      <div class="card" v-loading="isLoading">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.routes.wait_product') }}</h4>
        </div>
        <div class="card-content">
          <div class="row">
            <div class="col-xs-6">
               <div class="info-detail-package">
                <!-- <p>{{ $t('app.count_products') }}: <strong>{{ 0 }}</strong></p>
                <p>{{ $t('app.sum_product') }}: <strong>{{ 0 }}</strong></p> -->
              </div>
            </div>
          </div>


          <el-table :data="listData.data">
            <el-table-column type="selection" min-width="70" width="70">

            </el-table-column>
            <el-table-column :label="$t('app.product.string_id')" min-width="110" prop="product.string_id">

            </el-table-column>
            <el-table-column :label="$t('app.product.name')" min-width="150" prop="product.name">

            </el-table-column>
            <el-table-column :label="$t('app.package.name')" min-width="150" prop="package.name">

            </el-table-column>

            <el-table-column :label="$t('app.product.size_color')" min-width="150">
              <template slot-scope="props">
                <span>{{ props.row.color_of.toUpperCase() }} - {{ props.row.size_of.toUpperCase() }}</span>
              </template>
            </el-table-column>

            <el-table-column :label="$t('app.count')" min-width="110" prop="count">

            </el-table-column>

            <el-table-column :label="$t('app.bill.date_in')" min-width="150">
              <template slot-scope="props">
                <span>{{ props.row.created_at | moment('H:mm DD/MM/YYYY') }}</span>
              </template>
            </el-table-column>
            <el-table-column :label="$t('app.action')" min-width="100">
              <template slot-scope="props">
                <button class="btn btn-success btn-square btn-fill btn-icon" @click="doneWaitProperty(props.row.id)"><i class="fa fa-check"></i></button>
              </template>
            </el-table-column>

          </el-table>


        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import swal from 'sweetalert'
  import {getWaitProperty, doneWaitProperty} from '@/api/product'
  export default {
    data () {
      return {
        isLoading: false,
        listData: {}
      }
    },
    methods: {
      getWaitProperty () {
        this.isLoading = true
        getWaitProperty().then(e => {
          if (e.data.is_valid) {
            this.listData = e.data.data
          }
          this.isLoading = false
        })
      },
      doneWaitProperty (id) {
        swal({
          text: this.$t('app.product.alert_done_wait'),
          icon: 'info',
          buttons: true
        }).then(isOK => {
          if (isOK) {
            this.isLoading = true
            doneWaitProperty(id).then(e => {
              if (e.data.status === 'success') {
                this.getWaitProperty()
              }
              this.isLoading = false
            })
          }
        })
      }
    },
    mounted () {
      this.getWaitProperty()
    }
  }
</script>



