<template>
  <div class="row">
    <div class="col-md-12">
      <div class="card" v-loading="isLoading">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.routes.user') }}</h4>
        </div>
        <div class="card-content">
          <div class="float-right">
            <button class="btn btn-primary btn-fill btn-icon btn-square" @click="newPermission"><i class="fa fa-plus"></i> {{ $t('app.user.new') }}</button>
          </div>
          <h4 style="font-weight: 400;">{{ $t('app.user.list') }}</h4>
          <el-table :data="listUser">
            <el-table-column type="index">

            </el-table-column>
            <el-table-column :label="$t('app.user.name')" prop="name" min-width="100">

            </el-table-column>
            <el-table-column :label="$t('app.user.username')" prop="username" min-width="100">

            </el-table-column>
            <el-table-column :label="$t('app.user.status')" min-width="100">
              <template slot-scope="props">
                <span v-if="props.row.status === 'active'" class="label label-success">{{ $t('app.user.active') }}</span>
                <span v-else class="label label-danger">{{ $t('app.user.unactive') }}</span>
              </template>
            </el-table-column>
            <el-table-column :label="$t('app.user.roles')" min-width="100">
              <template slot-scope="props">
                <span>{{ props.row.roles.name }}</span>
              </template>
            </el-table-column>
            <el-table-column :label="$t('app.action')" min-width="100">
              <template slot-scope="props">
                <button class="btn btn-sm btn-warning btn-icon btn-fill btn-square" @click.prevent="handleEdit(props.row, props.$index)"><i class="fa fa-pencil-square-o"></i></button>
                <button class="btn btn-sm btn-danger btn-icon btn-fill btn-square" @click.prevent="handleDelete(props.row, props.$index)"><i class="fa fa-times"></i></button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {loadlistUser} from '@/api/user'
  export default {
    data () {
      return {
        listUser: [],
        isLoading: false
      }
    },
    methods: {
      loadlistUser () {
        this.isLoading = true
        loadlistUser().then(e => {
          if (e.data.is_valid) {
            this.listUser = e.data.data.filter(e => {
              return e.roles.super_admin !== 1
            })
          }
          this.isLoading = false
        })
      },
      handleEdit (row, index) {
        this.$router.push({
          name: 'edit_user',
          params: {
            id: row.id
          }
        })
      },
      newPermission () {
        this.$router.push({
          name: 'add_user'
        })
      }
    },
    mounted () {
      this.loadlistUser()
    }
  }
</script>



