import { login, logout } from '@/api/login'
import { getToken, setToken, getUser, setUser, unsetUser } from '@/utils/auth'

const user = {
  state: {
    token: getToken(),
    user: getUser()
  },
  mutations: {
    SET_TOKEN: (state, token) => {
      state.token = token
    },
    SET_USER: (state, user) => {
      state.user = user
    },
    UNSET_USER: (state) => {
      state.user = null
      state.token = null
    }
  },
  actions: {
    login ({commit}, userInfo) {
      return new Promise((resolve, reject) => {
        login(userInfo).then(e => {
          if (e.data.status === 'success') {
            commit('SET_TOKEN', e.data.token)
            commit('SET_USER', e.data.user)
            setToken(e.data.token)
            setUser(e.data.user)
          }
          resolve(e)
        }).catch(err => {
          reject(err)
        })
      })
    },
    logout ({commit}) {
      return new Promise((resolve, reject) => {
        logout().then(e => {
          commit('UNSET_USER')
          unsetUser()
          resolve(e)
        }).catch(err => reject(err))
      })
    },
    tokenNotInval ({commit}) {
      commit('UNSET_USER')
      unsetUser()
      return Promise.resolve('true')
    }
  }
}
export default user



