<template>
  <div class="row">
    <div class="col-md-12">
      <div class="card" v-loading="isLoading">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.bill.bill_in') }}</h4>
        </div>
        <div class="card-content">
          <div class="table-list-bill">
            <el-table :data="listProduct.data" style="width: 100%;">
              <el-table-column type="selection" min-width="20">

              </el-table-column>
              <el-table-column label="#" prop="id" min-width="20">

              </el-table-column>
              <el-table-column :label="$t('app.status')" min-width="100">
                <template slot-scope="props">
                  <span v-if="props.row.status === 'wait'" class="label label-warning">{{ $t('app.bill.status.wait') }}</span>
                  <span v-if="props.row.status === 'done'" class="label label-success">{{ $t('app.bill.status.done') }}</span>
                  <span v-if="props.row.status === 'wait_approve'" class="label label-danger">{{ $t('app.bill.status.wait_approve') }}</span>
                  <span v-if="props.row.note.length >= 1" class="label label-default" :title="props.row.note">{{ $t('app.bill.has_note') }}</span>
                </template>
              </el-table-column>
              <el-table-column :label="$t('app.bill.user')" prop="user.name" min-width="150">

              </el-table-column>
              <el-table-column :label="$t('app.bill.sum_price')" min-width="100" :formatter="formatPrice">

              </el-table-column>
              <el-table-column :label="$t('app.bill.date_in')" min-width=130>
                <template slot-scope="props">
                  <span>{{ props.row.updated_at | moment('H:mm DD/MM/YYYY') }}</span>
                </template>
              </el-table-column>
              <el-table-column
                :label="$t('app.action')" min-width="200">
                <template slot-scope="props">
                    <template v-if="props.row.status === 'wait_approve' && canApprove">
                      <button class="btn btn-success btn-icon btn-fill btn-square" @click="approveBill(props.row.id)"><i class="fa fa-check"></i></button>
                    </template>
                    <template v-if="(props.row.user_id === currentUser.id || canModify) && props.row.status === 'wait'">
                      <router-link class="btn btn-warning btn-icon btn-fill btn-square" :to="{name: 'input', params: {param: props.row.id}}"><i class="fa fa-arrow-right"></i></router-link>
                    </template>
                    <router-link class="btn btn-info btn-icon btn-fill btn-square" :to="{name: 'bill_in_detail', params: {id: props.row.id}}"><i class="fa fa-info"></i></router-link>
                    <template v-if="(props.row.status === 'wait' && props.row.user_id === currentUser.id) || canModify">
                      <a class="btn btn-danger btn-icon btn-fill btn-square" @click="deleteBill(props.row.id)"><i class="fa fa-times"></i></a>
                    </template>
                </template>
              </el-table-column>
            </el-table>
          </div>
          <div class="u-cus-pagination">
            <el-pagination v-if="listProduct.total > listProduct.per_page"
              background
              layout="prev, pager, next"
              :total="listProduct.total"
              :page-size="listProduct.per_page"
              :current-page="currentPage"
              @current-change="currentChange">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {getListBillIn, approveBill, deleteBill} from '@/api/bill'
  import swal from 'sweetalert'
  export default {
    data () {
      return {
        listProduct: [],
        isLoading: false,
        currentPage: 1,
        canApprove: false,
        canModify: false,
        currentUser: this.$store.getters.user
      }
    },
    mounted () {
      this.getListBill()
    },
    methods: {
      getListBill (index = 1) {
        this.isLoading = true
        getListBillIn(index).then(e => {
          if (e.data.is_valid) {
            this.listProduct = e.data.data
            this.listProduct.data = e.data.data.data.filter(e => {
              return e.status !== 'null' // remove status 'null' because it's null :)))
            })
            if (e.data.can_approve) {
              this.canApprove = true
            }
            if (e.data.can_modify) {
              this.canModify = true
            }
          }
          this.isLoading = false
        })
      },
      currentChange (index) {
        this.getListBill(index)
      },
      formatPrice (row, column) {
        let sum = 0
        row.product.forEach(e => {
          if (e.is_from_package === 0) {
            sum += (e.count * e.product.price_first)
          }
        })
        return this.$price(sum) + this.$t('app.price_suffix')
      },
      deleteBill (id) {
        swal({
          text: this.$t('app.bill.alert.delete'),
          icon: 'warning',
          buttons: true,
          dangerMode: true
        }).then(isOK => {
          if (isOK) {
            this.isLoading = true
            deleteBill(id).then(e => {
              if (e.data.status === 'success') {
                this.isLoading = false
                this.getListBill(this.currentPage)
              }
            })
          }
        })
      },
      approveBill (id) {
        swal({
          text: this.$t('app.bill.alert.approve'),
          icon: 'info',
          buttons: true
        }).then(isOK => {
          if (isOK) {
            this.isLoading = true
            approveBill(id).then(e => {
              if (e.data.status === 'success') {
                this.isLoading = false
                this.getListBill(this.currentPage)
              }
            })
          }
        })
      }
    }
  }
</script>



