import request from '@/utils/request'

export function loadListPermission () {
  return request({
    url: '/roles/list',
    method: 'get'
  })
}

export function loadPermission (id) {
  return request({
    url: '/roles/get/' + id,
    method: 'get'
  })
}

export function savePermission (id, data) {
  return request({
    url: '/roles/save/' + id,
    method: 'post',
    data: data
  })
}

export function createPermission (data) {
  return request({
    url: '/roles/create',
    method: 'post',
    data: data
  })
}



