<template>
<div class="row">
  <div class="col-lg-6 col-md-12">
    <div class="card">
      <div class="card-header">
        <h4 class="card-title">{{ $t('app.routes.add_customer') }}</h4>
      </div>
      <div class="card-content">
        <div class="row" v-loading="isLoading">
          <div class="col-md-12">
            <form @submit.prevent="submitCustomer">
              <div class="form-group">
                <label>{{ $t('app.customer.name') }}</label>
                <input type="text" :placeholder="$t('app.customer.name')" name="name" class="form-control" required>
              </div>
              <div class="form-group">
                <label>{{ $t('app.customer.phone') }}</label>
                <input type="text" :placeholder="$t('app.customer.phone')" name="phone" class="form-control" required>
              </div>
              <div class="form-group">
                <label>{{ $t('app.customer.address') }}</label>
                <input type="text" :placeholder="$t('app.customer.address')" name="address" class="form-control">
              </div>
              <div class="form-group">
                <label>{{ $t('app.customer.zalo') }}</label>
                <input type="text" :placeholder="$t('app.customer.zalo')" name="zalo" class="form-control">
              </div>
              <div class="form-group">
                <label>{{ $t('app.customer.facebook') }}</label>
                <input type="text" :placeholder="$t('app.customer.facebook')" name="facebook" class="form-control">
              </div>
              <div class="form-group">
                <label>{{ $t('app.customer.type') }}</label>
                <select name="type" class="form-control">
                  <template v-for="type in types">
                    <option :value="type.value">{{ type.name }}</option>
                  </template>
                </select>
              </div>
              <div class="float-right">
                <button type="submit" class="btn btn-fill btn-sm btn-success">{{ $t('app.save') }}</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
  import {createCustomer} from '@/api/customer'
  export default {
    data () {
      return {
        isLoading: false,
        types: [
          {
            name: this.$t('app.customer.retail'),
            value: 'retail'
          },
          {
            name: this.$t('app.customer.wholesale'),
            value: 'wholesale'
          }
        ],
        selectType: 'retail'
      }
    },
    methods: {
      submitCustomer (e) {
        this.isLoading = true
        let formData = new FormData(e.target)
        let form = e.target
        createCustomer(formData).then(e => {
          if (e.data.status === 'success') {
            form.reset()
          }
          this.isLoading = false
        })
      }
    }
  }
</script>



