<template>
<div class="row">
  <template>
    <div class="col-md-12">
      <div class="card" v-loading="isLoading">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.supplier.all_supplier') }}</h4>
        </div>
        <div class="card-content">
          <el-table :data="listSupplier.data">
            <el-table-column type="index" min-width="70" width="70">

            </el-table-column>
            <el-table-column prop="name"
                             :label="$t('app.supplier.name')"
                             min-width="200">
              <template slot-scope="props">
                <template v-if="props.row.name === 'root'">
                  <span>{{ $t('app.supplier.root') }}</span>
                </template>
                <template v-else>
                  <span>{{ props.row.name }}</span>
                </template>
              </template>
            </el-table-column>
            <el-table-column prop="address"
                             :label="$t('app.supplier.address')"
                             min-width="300">
            </el-table-column>
            <el-table-column prop="phone"
                             :label="$t('app.supplier.phone')"
                             min-width="200">
            </el-table-column>
            <el-table-column prop="product_count" :label="$t('app.count')" min-width="100">

            </el-table-column>
            <el-table-column
              :label="$t('app.action')" min-width="200">
              <template slot-scope="props">
                  <a class="btn btn-info btn-icon btn-fill btn-square" @click="handleInfo(props.$index, props.row)"><i class="fa fa-info"></i></a>
                <template v-if="props.row.name != 'root'">
                  <template v-if="canModify">
                    <a class="btn btn-warning btn-icon btn-fill btn-square" @click="handleEdit(props.$index, props.row)"><i class="fa fa-pencil-square-o"></i></a>
                    <a class="btn btn-danger btn-icon btn-fill btn-square" @click="handleDelete(props.$index, props.row)"><i class="fa fa-times"></i></a>
                  </template>
                </template>
              </template>
            </el-table-column>
          </el-table>

          <div class="u-cus-pagination">
            <el-pagination
              background
              layout="prev, pager, next"
              :total="listSupplier.total"
              :page-size="listSupplier.per_page"
              :current-page="currentPage"
              @current-change="currentChange">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </template>
</div>
</template>

<script>
  import Vue from 'vue'
  import swal from 'sweetalert'
  import {Table, TableColumn, Pagination} from 'element-ui'
  import {loadSupplier, changeStatusSupplier, deleteSupplier} from '@/api/supplier'
  Vue.use(Table)
  Vue.use(TableColumn)
  Vue.use(Pagination)
  export default {
    data () {
      return {
        isLoading: false,
        listSupplier: [],
        currentPage: 1,
        switches: {
          isActiveSupplier: true
        },
        canModify: false
      }
    },
    mounted () {
      this.loadSupplier()
    },
    methods: {
      currentChange (index) {
        this.loadSupplier(index)
      },
      loadSupplier (query = null) {
        this.listSupplier = []
        this.isLoading = true
        loadSupplier(query).then(e => {
          if (e.data.is_valid) {
            this.listSupplier = e.data.supplier
          }
          if (e.data.is_modify) {
            this.canModify = true
          }
          this.isLoading = false
        })
      },
      handleStatus (index, row) {
        let temp = row.status
        if (temp === 'active') {
          row.status = 'lock'
        } else {
          row.status = 'active'
        }
        this.isLoading = true
        changeStatusSupplier(row.id).then(e => {
          if (e.data.status !== 'success') {
            row.status = temp
          }
          this.isLoading = false
        })
      },
      handleEdit (index, row) {
        this.$router.push('/admin/supplier/edit/' + row.id)
      },
      handleDelete (index, row) {
        let html = document.createElement('div')
        // let id = row.id
        html.innerHTML = this.$t('app.supplier.delete', {name: row.name})
        swal({
          icon: 'warning',
          buttons: true,
          dangerMode: true,
          content: html
        }).then(isDelete => {
          if (isDelete) {
            deleteSupplier()
          }
        })
      },
      handleInfo (index, row) {
        this.$router.push({
          name: 'detail_supplier',
          params: {
            id: row.id
          }
        })
      }
    },
    watch: {
      '$route.params.param': function () {
        this.loadTypeSupplier()
      }
    }
  }
</script>



