<template>
<div class="row">
  <template>
    <div class="col-md-12">
      <div class="card" v-loading="isLoading">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.customer.all') }}</h4>
        </div>
        <div class="card-content">
          <el-table :data="listData.data">
            <el-table-column type="index" min-width="70" width="70">

            </el-table-column>
            <el-table-column :label="$t('app.customer.name')" min-width="200">
              <template slot-scope="props">
                <template v-if="props.row.name === 'root'">
                  <span>{{ $t('app.customer.root') }}</span>
                </template>
                <template v-else>
                  <span>{{ props.row.name }}</span>
                </template>
              </template>
            </el-table-column>
            <el-table-column :label="$t('app.customer.phone')" prop="phone" min-width="150">

            </el-table-column>
            <el-table-column :label="$t('app.customer.address')" prop="address" min-width="150">

            </el-table-column>
            <el-table-column :label="$t('app.customer.visit_count')" prop="visited_count" min-width="150">

            </el-table-column>
            <el-table-column :label="$t('app.action')" min-width="100">
              <template slot-scope="props">
                <template v-if="props.row.name != 'root'">
                  <a class="btn btn-warning btn-icon btn-fill btn-square" @click="handleEdit(props.$index, props.row)"><i class="fa fa-pencil-square-o"></i></a>
                  <a class="btn btn-danger btn-icon btn-fill btn-square" @click="handleDelete(props.$index, props.row)"><i class="fa fa-times"></i></a>
                </template>
              </template>
            </el-table-column>
          </el-table>

          <div class="u-cus-pagination">
            <el-pagination v-if="listData.total > listData.per_page"
              background
              layout="prev, pager, next"
              :total="listData.total"
              :page-size="listData.per_page"
              :current-page="currentPage"
              @current-change="currentChange">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </template>
</div>
</template>

<script>
  import {getListCustomer, deleteCustomer} from '@/api/customer'
  import swal from 'sweetalert'
  export default {
    data () {
      return {
        isLoading: false,
        listData: {},
        canModify: false,
        currentPage: 1
      }
    },
    methods: {
      getListCustomer (index = 0) {
        this.isLoading = true
        getListCustomer(index).then(e => {
          if (e.data.is_valid) {
            this.listData = e.data.customer
            if (e.data.is_modify) {
              this.canModify = true
            }
            this.isLoading = false
          }
        })
      },
      currentChange (index) {
        this.getListCustomer(index)
      },
      handleEdit (index, row) {
        this.$router.push({
          name: 'edit_customer',
          params: {
            id: row.id
          }
        })
      },
      handleDelete (index, row) {
        let html = document.createElement('div')
        // let id = row.id
        html.innerHTML = this.$t('app.customer.delete', {name: row.name})
        swal({
          icon: 'warning',
          buttons: true,
          dangerMode: true,
          content: html
        }).then(isDelete => {
          if (isDelete) {
            this.isLoading = true
            deleteCustomer(row.id).then(e => {
              if (e.data.status === 'success') {
                this.getListCustomer(this.currentPage)
              }
              this.isLoading = false
            })
          }
        })
      }
    },
    mounted () {
      this.getListCustomer()
    }
  }
</script>

<style>
  .u-cus-pagination li.more {
      float: unset !important;
      z-index: unset !important;
      display: inline !important;
      padding: 0 !important;
  }
</style>



