<template>
<div class="row">
  <div class="col-lg-6 col-md-12">
    <div class="card">
      <div class="card-header">
        <h4 class="card-title">{{ $t('app.product_type.add') }}</h4>
      </div>
      <div class="card-content" v-loading="isLoading">
        <div class="row">
          <div class="col-md-12">
            <form @submit.prevent="submit">
              <div class="form-group">
                <label>{{ $t('app.product_type.name') }}</label>
                <input type="text" :placeholder="$t('app.product_type.name')" name="name" class="form-control" required>
              </div>
              <div class="form-group">
                <label>{{ $t('app.description') }}</label>
                <textarea name="description" class="form-control" rows="4"></textarea>
              </div>
              <div class="float-right">
                <!-- HIDDEN -->
                <button type="submit" class="btn btn-fill btn-sm btn-success">{{ $t('app.save') }}</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
  import {createProductType} from '@/api/product-type'
  export default {
    data () {
      return {
        isLoading: false
      }
    },
    methods: {
      submit (e) {
        this.isLoading = true
        let formData = new FormData(e.target)
        let form = e.target
        createProductType(formData).then(e => {
          if (e.data.status === 'success') {
            form.reset()
          }
          this.isLoading = false
        })
      }
    }
  }
</script>



