import Vue from 'vue'
import VueRouter from 'vue-router'
import i18n from '@/lang'
import initRoute from './init'
import DashboardLayout from '../components/Dashboard/Layout/DashboardLayout.vue'
// GeneralViews
import NotFound from '../components/GeneralViews/NotFoundPage.vue'
// Dashboard pages
import Overview from 'src/components/Dashboard/Views/Dashboard/Overview.vue'
// Auth page
import Login from 'src/components/Dashboard/Views/Pages/Login.vue'
import Lock from 'src/components/Dashboard/Views/Pages/Lock.vue'
// PRODUCT
import ProductIn from 'src/components/Dashboard/Views/App/Product/ProductIn.vue'
import ListProduct from 'src/components/Dashboard/Views/App/Product/ListProduct.vue'
import EditProduct from 'src/components/Dashboard/Views/App/Product/EditProduct.vue'
import DetailProduct from 'src/components/Dashboard/Views/App/Product/DetailProduct.vue'
import ProductInPackage from 'src/components/Dashboard/Views/App/Product/ProductInPackage.vue'
import WaitProduct from 'src/components/Dashboard/Views/App/Product/WaitProduct.vue'
// Supplier
import AddSupplier from 'src/components/Dashboard/Views/App/Supplier/AddSupplier.vue'
import HomeSupplier from 'src/components/Dashboard/Views/App/Supplier/HomeSupplier.vue'
import EditSupplier from 'src/components/Dashboard/Views/App/Supplier/EditSupplier.vue'
import DetailSupplier from 'src/components/Dashboard/Views/App/Supplier/DetailSupplier.vue'
// Properties Type
import AddProductType from 'src/components/Dashboard/Views/App/ProductType/AddProductType.vue'
import HomeProductType from 'src/components/Dashboard/Views/App/ProductType/HomeProductType.vue'
import EditProductType from 'src/components/Dashboard/Views/App/ProductType/EditProductType.vue'
import DetailProductType from 'src/components/Dashboard/Views/App/ProductType/DetailProductType.vue'
// BILL
import BillIn from 'src/components/Dashboard/Views/App/Bill/BillIn.vue'
import BillOut from 'src/components/Dashboard/Views/App/Bill/BillOut.vue'
import BillInDetail from 'src/components/Dashboard/Views/App/Bill/BillInDetail.vue'
import BillOutDetail from 'src/components/Dashboard/Views/App/Bill/BillOutDetail.vue'
import SettingBill from 'src/components/Dashboard/Views/App/Bill/SettingBill.vue'
// Sale
import SaleRetail from 'src/components/Dashboard/Views/App/Sale/SaleRetail.vue'
import WholeSale from 'src/components/Dashboard/Views/App/Sale/WholeSale.vue'
// WAREhousE
import AddWarehouse from 'src/components/Dashboard/Views/App/Warehouse/AddWarehouse.vue'
import HomeWarehouse from 'src/components/Dashboard/Views/App/Warehouse/HomeWarehouse.vue'
import EditWarehouse from 'src/components/Dashboard/Views/App/Warehouse/EditWarehouse.vue'
// SUPER ADMIN
import Permission from 'src/components/Dashboard/Views/App/Admin/Permission.vue'
import EditPermission from 'src/components/Dashboard/Views/App/Admin/EditPermission.vue'
import AddPermission from 'src/components/Dashboard/Views/App/Admin/AddPermission.vue'
import User from 'src/components/Dashboard/Views/App/Admin/User.vue'
import EditUser from 'src/components/Dashboard/Views/App/Admin/EditUser.vue'
import AddUser from 'src/components/Dashboard/Views/App/Admin/AddUser.vue'
// CUSTOMER
import AddCustomer from 'src/components/Dashboard/Views/App/Customer/AddCustomer.vue'
import HomeCustomer from 'src/components/Dashboard/Views/App/Customer/HomeCustomer.vue'
import EditCustomer from 'src/components/Dashboard/Views/App/Customer/EditCustomer.vue'
// SALE OF
import AddSaleOf from 'src/components/Dashboard/Views/App/SaleOf/AddSaleOf.vue'
import HomeSaleOf from 'src/components/Dashboard/Views/App/SaleOf/HomeSaleOf.vue'
// SPEND
import AddSpend from 'src/components/Dashboard/Views/App/Spend/AddSpend.vue'
import HomeSpend from 'src/components/Dashboard/Views/App/Spend/HomeSpend.vue'


Vue.use(VueRouter)

let loginPage = {
  path: '/login',
  name: 'login',
  component: Login
}

let lockPage = {
  path: '/lock',
  name: 'Lock',
  component: Lock
}
// Supplier Router
let supplier = [
  {
    path: 'add-supplier',
    name: 'add_supplier',
    component: AddSupplier
  },
  {
    path: 'supplier',
    redirect: '/admin/supplier/all'
  },
  {
    path: 'supplier/all',
    name: 'supplier',
    component: HomeSupplier
  },
  {
    path: 'supplier/edit/:id',
    name: 'edit_supplier',
    component: EditSupplier
  },
  {
    path: 'supplier/deail/:id',
    name: 'detail_supplier',
    component: DetailSupplier
  }
]
// Product Router
let product = [
  {
    path: 'in-product',
    redirect: '/admin/in-product/new'
  },
  {
    path: 'in-product/:param',
    name: 'input',
    component: ProductIn
  },
  {
    path: 'product',
    name: 'product',
    component: ListProduct
  },
  {
    path: 'product/edit/:id',
    name: 'edit_product',
    component: EditProduct
  },
  {
    path: 'product/detail/:id',
    name: 'detail_product',
    component: DetailProduct
  },
  {
    path: 'product/package/in/:id',
    name: 'in_package',
    component: ProductInPackage
  },
  {
    path: 'wait-product',
    name: 'wait_product',
    component: WaitProduct
  }
]
// Product Type Router
let productType = [
  {
    path: 'add-product-type',
    name: 'add_product_type',
    component: AddProductType
  },
  {
    path: 'product-type',
    name: 'product_type',
    component: HomeProductType
  },
  {
    path: 'product-type/edit/:id',
    name: 'edit_product_type',
    component: EditProductType
  },
  {
    path: 'product-type/detail/:id',
    name: 'detail_product_type',
    component: DetailProductType
  }
]

let bill = [
  {
    path: 'bill-in',
    name: 'bill_in',
    component: BillIn
  },
  {
    path: 'bill-in/:id',
    name: 'bill_in_detail',
    component: BillInDetail
  },
  {
    path: 'bill-out',
    name: 'bill_out',
    component: BillOut
  },
  {
    path: 'bill-out/:id',
    name: 'bill_out_detail',
    component: BillOutDetail
  },
  {
    path: 'setting-bill',
    name: 'setting_bill',
    component: SettingBill
  }
]

let sale = [
  {
    path: 'sale/retail',
    name: 'sale_retail',
    component: SaleRetail
  },
  {
    path: 'sale/wholesale',
    name: 'wholesale',
    component: WholeSale
  }
]

let warehouse = [
  {
    path: 'add-warehouse',
    name: 'add_warehouse',
    component: AddWarehouse
  },
  {
    path: 'warehouse',
    name: 'warehouse',
    component: HomeWarehouse
  },
  {
    path: 'warehouse/edit/:id',
    name: 'edit_warehouse',
    component: EditWarehouse
  }
]


let superAdmin = [
  {
    path: 'permission',
    name: 'permission',
    component: Permission
  },
  {
    path: 'permission/edit/:id',
    name: 'edit_permission',
    component: EditPermission
  },
  {
    path: 'permission/add',
    name: 'add_permission',
    component: AddPermission
  },
  {
    path: 'users',
    name: 'users',
    component: User
  },
  {
    path: 'users/edit/:id',
    name: 'edit_user',
    component: EditUser
  },
  {
    path: 'users/add',
    name: 'add_user',
    component: AddUser
  }
]

let customer = [
  {
    path: 'add-customer',
    name: 'add_customer',
    component: AddCustomer
  },
  {
    path: 'customer',
    name: 'all_customer',
    component: HomeCustomer
  },
  {
    path: 'customer/edit/:id',
    name: 'edit_customer',
    component: EditCustomer
  }
]

let saleOf = [
  {
    path: 'add-sale-of',
    name: 'add_sale_of',
    component: AddSaleOf
  },
  {
    path: 'sale-of',
    name: 'list_sale_of',
    component: HomeSaleOf
  }
]

let spend = [
  {
    path: 'add-spend',
    name: 'add_spend',
    component: AddSpend
  },
  {
    path: 'spend',
    name: 'list_spend',
    component: HomeSpend
  }
]


let adminRouter = {
  path: '/admin',
  component: DashboardLayout,
  redirect: '/admin/overview',
  children: [
    {
      path: 'overview',
      name: 'overview',
      component: Overview
    },
    ...product,
    ...supplier,
    ...productType,
    ...bill,
    ...sale,
    ...warehouse,
    ...superAdmin,
    ...customer,
    ...saleOf,
    ...spend
  ],
  meta: {
    requiredAuth: true
  }
}

const routes = [
  {
    path: '/',
    component: DashboardLayout,
    redirect: '/admin/overview',
    meta: {
      requiredAuth: true
    }
  },
  loginPage,
  lockPage,
  adminRouter,
  {path: '*', component: NotFound}
]

const router = new VueRouter({
  routes, // short for routes: routes
  linkActiveClass: 'active'
})
router.beforeEach(initRoute)
router.afterEach((to, from) => {
  document.title = i18n.t('app.routes.' + to.name)
})


/**
 * Asynchronously load view (Webpack Lazy loading compatible)
 * The specified component must be inside the Views folder
 * @param  {string} name  the filename (basename) of the view to load.
 function view(name) {
   var res= require('../components/Dashboard/Views/' + name + '.vue');
   return res;
};**/

export default router



