const getters = {
  token: state => state.user.token,
  host: state => state.app.host,
  user: state => state.user.user,
  isLoading: state => state.app.isLoading,
  warehouses: state => state.app.warehouses,
  reRender: state => state.app.reRender,
  selectedWarehouse: state => state.app.selectedWarehouse,
  isValidSelectTypeProduct: state => state.app.isValidSelectTypeProduct,
  valueSelectTypeProduct: state => state.app.valueSelectTypeProduct,
  isRootWarehouse: state => state.app.isRootWarehouse,
  listShower: state => state.app.listShower
}

export default getters



