import request from '@/utils/request'

export function getBillIn () {
  return request({
    url: '/app/bill-in/create',
    method: 'post'
  })
}

export function submitProduct (data) {
  return request({
    url: '/app/product/create',
    method: 'post',
    data: data
  })
}

export function getBillWithId (data) {
  return request({
    url: '/app/bill-in/get/' + data,
    method: 'get'
  })
}

export function deleteMoreBill (data) {
  return request({
    url: '/app/bill-in/delete-more-bill/' + data,
    method: 'post'
  })
}

export function uniqueProduct (name, type) {
  return request({
    url: '/app/product/check-unique?name=' + name + '&type=' + type,
    method: 'get'
  })
}


export function searchProduct (name, warehouseID = '*') {
  return request({
    url: '/app/product/search?name=' + name + '&warehouse_id=' + warehouseID,
    method: 'get'
  })
}

export function saveBill (id, data) {
  return request({
    url: '/app/bill-in/done/' + id,
    method: 'post',
    data: data
  })
}

export function getListProduct (query, index, warehouseID = '', type = 'all') {
  return request({
    url: '/app/product/list?q=' + query + '&page=' + index + '&warehouse_id=' + warehouseID + '&type=' + type,
    method: 'get'
  })
}

export function loadListPropertiesProduct (id, warehouseID = '', type = 'all') {
  return request({
    url: '/app/product/get/' + id + '?warehouse_id=' + warehouseID + '&type=' + type,
    method: 'get'
  })
}

export function submitEditProduct (id, data) {
  return request({
    url: '/product/edit/' + id,
    method: 'post',
    data: data
  })
}

export function inputFormPackage (id, data) {
  return request({
    url: '/app/product/package/add/' + id,
    method: 'post',
    data: data
  })
}

export function deleteProduct (id) {
  return request({
    url: 'product/delete/' + id,
    method: 'post'
  })
}

export function saveProperty (row) {
  return request({
    url: 'product/property/edit',
    method: 'post',
    data: row
  })
}

export function deleteProperty (id) {
  return request({
    url: 'product/property/delete/' + id,
    method: 'post'
  })
}

export function getListProductByProductType (id, index) {
  return request({
    url: 'product/product-type/' + id + '?page=' + index,
    method: 'get'
  })
}


export function getListProductBySupplider (id, index) {
  return request({
    url: 'product/supplier/' + id + '?page=' + index,
    method: 'get'
  })
}

export function getWaitProperty () {
  return request({
    url: 'app/product/wait'
  })
}

export function doneWaitProperty (id) {
  return request({
    url: 'app/product/wait/done/' + id,
    method: 'post'
  })
}



