<template>
  <div class="row">
    <div class="col-md-12">
      <div class="card" v-loading="isLoading">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.bill.bill_out') }}</h4>
        </div>
        <div class="card-content">
          <div class="table-list-bill">
            <el-table :data="listBill.data" style="width: 100%;">
              <el-table-column type="selection" min-width="20" with="20">

              </el-table-column>
              <el-table-column label="#" prop="id" min-width="20" with="20">

              </el-table-column>

              <el-table-column :label="$t('app.user.name')" prop="user.name">

              </el-table-column>

              <el-table-column :label="$t('app.customer.name')">
                <template slot-scope="props">
                  <span v-if="props.row.customer.name === 'root'">{{ $t('app.customer.root') }}</span>
                  <span v-else>{{ props.row.customer.name }}</span>
                </template>
              </el-table-column>

              <el-table-column :label="$t('app.bill.sum_price')" :formatter="formatPrice">
              </el-table-column>

              <el-table-column :label="$t('app.warehouse.name')">
                <template slot-scope="props">
                  <span v-if="props.row.warehouse.name === 'root'">{{ $t('app.warehouse.root') }}</span>
                  <span v-else>{{ props.row.warehouse.name }}</span>
                </template>
              </el-table-column>

              <el-table-column :label="$t('app.bill.date_in')">
                <template slot-scope="props">
                  <span>{{ props.row.created_at | moment('H:mm DD/MM/YYYY') }}</span>
                </template>
              </el-table-column>

              <el-table-column :label="$t('app.status')">
                <template slot-scope="props">
                  <span v-if="props.row.type === 'retail'" class="label label-success">{{ $t('app.sale.retail') }}</span>
                  <span v-if="props.row.type === 'wholesale'" class="label label-primary">{{ $t('app.sale.wholesale') }}</span>
                  <template v-if="props.row.note !== null && props.row.note.length >= 1">
                    <br>
                    <span class="label label-default" :title="props.row.note" style="margin-top: 5px">{{ $t('app.bill.has_note') }}</span>
                  </template>
                </template>
              </el-table-column>

              <el-table-column
                :label="$t('app.action')">
                <template slot-scope="props">
                    <router-link class="btn btn-info btn-icon btn-fill btn-square" :to="{name: 'bill_out_detail', params: {id: props.row.id}}"><i class="fa fa-info"></i></router-link>
                    <template v-if="props.row.user_id === currentUser.id || canModify">
                      <a class="btn btn-danger btn-icon btn-fill btn-square" @click="deleteBill(props.row.id)"><i class="fa fa-times"></i></a>
                    </template>
                </template>
              </el-table-column>
            </el-table>
          </div>
          <div class="u-cus-pagination">
            <el-pagination v-if="listBill.total > listBill.per_page"
              background
              layout="prev, pager, next"
              :total="listBill.total"
              :page-size="listBill.per_page"
              :current-page="currentPage"
              @current-change="currentChange">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
  import {getListBillOut, deleteBillOut} from '@/api/bill'
  import swal from 'sweetalert'
  export default {
    data () {
      return {
        listBill: [],
        isLoading: false,
        currentUser: this.$store.getters.user,
        canModify: false,
        currentPage: 0
      }
    },
    methods: {
      getListBill (index = 0) {
        this.isLoading = true
        getListBillOut(index, this.selectedWarehouse, this.valueSelectTypeProduct).then(e => {
          if (e.data.is_valid) {
            this.listBill = e.data.data
            if (e.data.can_modify) {
              this.canModify = true
            }
          }
          this.isLoading = false
        })
      },
      formatPrice (row, column) {
        let sum = 0
        row.product.forEach(e => {
          sum += (e.count * e.price)
        })
        sum -= row.price_of
        return this.$price(sum) + this.$t('app.price_suffix')
      },
      deleteBill (id) {
        swal({
          text: this.$t('app.bill.alert.delete'),
          icon: 'warning',
          buttons: true,
          dangerMode: true
        }).then(isOK => {
          if (isOK) {
            this.isLoading = true
            deleteBillOut(id).then(e => {
              this.isLoading = false
              if (e.data.status === 'success') {
                this.getListBill(this.currentPage)
              }
            })
          }
        })
      },
      currentChange (index) {
        this.getListBill(index)
      }
    },
    computed: {
      selectedWarehouse () {
        return this.$store.getters.selectedWarehouse
      },
      valueSelectTypeProduct: function () {
        return this.$store.getters.valueSelectTypeProduct
      }
    },
    mounted () {
      this.getListBill()
    }
  }
</script>



