<template>
<div class="row">
  <template>
    <div class="col-md-6">
      <div class="card" v-loading="isLoading">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.product_type.all') }}</h4>
        </div>
        <div class="card-content">
          <el-table :data="listData.data">
            <el-table-column type="index" min-width="70" width="70">

            </el-table-column>
            <el-table-column :label="$t('app.product_type.name')">
              <template slot-scope="props">
                <template v-if="props.row.name === 'root'">
                  <span>{{ $t('app.supplier.root') }}</span>
                </template>
                <template v-else>
                  <span>{{ props.row.name }}</span>
                </template>
              </template>
            </el-table-column>
            <el-table-column :label="$t('app.count')" prop="count_product_count">

            </el-table-column>
            <el-table-column :label="$t('app.action')">
              <template slot-scope="props">
                  <a class="btn btn-info btn-icon btn-fill btn-square" @click="handleInfo(props.$index, props.row)"><i class="fa fa-info"></i></a>
                <template v-if="props.row.name != 'root' && canModify">
                  <a class="btn btn-warning btn-icon btn-fill btn-square" @click="handleEdit(props.$index, props.row)"><i class="fa fa-pencil-square-o"></i></a>
                  <a class="btn btn-danger btn-icon btn-fill btn-square" @click="handleDelete(props.$index, props.row)"><i class="fa fa-times"></i></a>
                </template>
              </template>
            </el-table-column>
          </el-table>

          <div class="u-cus-pagination">
            <el-pagination v-if="listData.total > listData.per_page"
              background
              layout="prev, pager, next"
              :total="listData.total"
              :page-size="listData.per_page"
              :current-page="currentPage"
              @current-change="currentChange">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </template>
</div>
</template>

<script>
  import {getListProductType, deleteProductType} from '@/api/product-type'
  import swal from 'sweetalert'
  export default {
    data () {
      return {
        isLoading: false,
        listData: {},
        canModify: false,
        currentPage: 1
      }
    },
    methods: {
      getProductTypes (index = 0) {
        this.isLoading = true
        getListProductType(index).then(e => {
          if (e.data.is_valid) {
            this.listData = e.data.product_type
            if (e.data.is_modify) {
              this.canModify = true
            }
            this.isLoading = false
          }
        })
      },
      currentChange (index) {
        this.getProductTypes(index)
      },
      handleInfo (index, row) {
        this.$router.push({
          name: 'detail_product_type',
          params: {
            id: row.id
          }
        })
      },
      handleEdit (index, row) {
        this.$router.push({
          name: 'edit_product_type',
          params: {
            id: row.id
          }
        })
      },
      handleDelete (index, row) {
        let html = document.createElement('div')
        // let id = row.id
        html.innerHTML = this.$t('app.product_type.delete', {name: row.name})
        swal({
          icon: 'warning',
          buttons: true,
          dangerMode: true,
          content: html
        }).then(isDelete => {
          if (isDelete) {
            this.isLoading = true
            deleteProductType(row.id).then(e => {
              if (e.data.status === 'success') {
                this.getProductTypes(this.currentPage)
              }
              this.isLoading = false
            })
          }
        })
      }
    },
    mounted () {
      this.getProductTypes()
    }
  }
</script>

<style>
  .u-cus-pagination li.more {
      float: unset !important;
      z-index: unset !important;
      display: inline !important;
      padding: 0 !important;
  }
</style>



