<template>
<div class="row">
  <template>
    <div class="col-md-12">
      <div class="card" v-loading="isLoading">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.warehouse.list') }}</h4>
        </div>
        <div class="card-content">
          <el-table :data="listWarehouse.data">
            <el-table-column type="index" min-width="70" width="70">

            </el-table-column>
            <el-table-column prop="name"
                             :label="$t('app.warehouse.name')"
                             min-width="200">
              <template slot-scope="props">
                <template v-if="props.row.name === 'root'">
                  <span>{{ $t('app.warehouse.root') }}</span>
                </template>
                <template v-else>
                  <span>{{ props.row.name }}</span>
                </template>
              </template>
            </el-table-column>
            <el-table-column prop="address"
                             :label="$t('app.warehouse.address')"
                             min-width="300">
            </el-table-column>
            <el-table-column prop="phone"
                             :label="$t('app.warehouse.phone')"
                             min-width="200">
            </el-table-column>
            <el-table-column prop="product_count" :label="$t('app.count')" min-width="100">

            </el-table-column>
            <el-table-column
              :label="$t('app.action')" min-width="200">
              <template slot-scope="props">
                 <!--  <a class="btn btn-info btn-icon btn-fill btn-square" @click="handleInfo(props.$index, props.row)"><i class="fa fa-info"></i></a> -->
                <template v-if="props.row.name != 'root'">
                  <a class="btn btn-warning btn-icon btn-fill btn-square" @click="handleEdit(props.$index, props.row)"><i class="fa fa-pencil-square-o"></i></a>
                  <a class="btn btn-danger btn-icon btn-fill btn-square" @click="handleDelete(props.$index, props.row)"><i class="fa fa-times"></i></a>
                </template>
              </template>
            </el-table-column>
          </el-table>

          <div class="u-cus-pagination">
            <el-pagination v-if="listWarehouse.total > listWarehouse.per_page"
              background
              layout="prev, pager, next"
              :total="listWarehouse.total"
              :page-size="listWarehouse.per_page"
              :current-page="currentPage"
              @current-change="currentChange">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </template>
</div>
</template>

<script>
  import Vue from 'vue'
  // import swal from 'sweetalert'
  import {Table, TableColumn, Pagination} from 'element-ui'
  import {loadWarehouse} from '@/api/warehouse'
  Vue.use(Table)
  Vue.use(TableColumn)
  Vue.use(Pagination)
  export default {
    data () {
      return {
        isLoading: false,
        listWarehouse: [],
        currentPage: 1,
        switches: {
          isActiveSupplier: true
        }
      }
    },
    mounted () {
      this.loadWarehouse(true)
    },
    methods: {
      currentChange (index) {
        this.loadWarehouse(true, index)
      },
      loadWarehouse (isPagi = null, index = null) {
        this.listSupplier = []
        this.isLoading = true
        loadWarehouse(isPagi, index).then(e => {
          if (e.data.is_valid) {
            this.listWarehouse = e.data.warehouse
            this.isLoading = false
          }
        })
      },
      handleEdit (index, row) {
        this.$router.push('/admin/warehouse/edit/' + row.id)
      },
      handleDelete (index, row) {

      }
    },
    watch: {
      '$route.params.param': function () {
        this.loadTypeSupplier()
      }
    }
  }
</script>



