<template>
  <div class="row">
    <div class="col-md-12 col-lg-6">
      <div class="card" v-loading="isLoading" v-if="'name' in permission">
        <div class="card-header">
          <h4 class="card-title">{{ permission.name }}</h4>
        </div>
        <div class="card-content">
          <div class="roles-group">
            <template v-for="item in listVisable">
              <p>{{$t('app.permission.' + item)}}:
                <el-switch
                  v-model="permission[item]"
                  active-color="#13ce66"
                  inactive-color="#ff4949">
                </el-switch>
              </p>
            </template>
          </div>
          <div class="permission-group">
            <el-table :data="permission.permission">
              <el-table-column type="index" min-width="800" width="80">

              </el-table-column>
              <el-table-column :label="$t('app.permission.name')" min-width="100">
                <template slot-scope="props">
                  <span>{{ $t('app.permission.' + props.row.name) }}</span>
                </template>
              </el-table-column>
              <el-table-column :label="$t('app.permission.view')" min-width="100">
                <template slot-scope="props">
                  <el-switch v-model="props.row.view"
                  active-color="#13ce66"
                  inactive-color="#ff4949">
                  </el-switch>
                </template>
              </el-table-column>
              <el-table-column :label="$t('app.permission.modify')" min-width="100">
                <template slot-scope="props">
                  <el-switch v-model="props.row.modify"
                  active-color="#13ce66"
                  inactive-color="#ff4949">
                  </el-switch>
                </template>
              </el-table-column>
            </el-table>
          </div>
          <div class="float-right" style="margin-top: 20px;">
            <button type="submit" class="btn btn-fill btn-square btn-success btn-wd" @click="savePermission">{{ $t('app.save') }}</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {loadPermission, savePermission} from '@/api/permission'
  export default {
    data () {
      return {
        isLoading: false,
        permission: {},
        listVisable: [
          'can_approve', 'input', 'input_package', 'retail', 'view_price_in', 'wholesale'
        ]
      }
    },
    methods: {
      loadPermission (id) {
        this.isLoading = true
        loadPermission(id).then(e => {
          if (e.data.is_valid) {
            let result = e.data.data
            this.listVisable.forEach(e => {
              result[e] = !!result[e]
            })
            result.permission.forEach(e => {
              e.name = e.name.replace('-', '_')
            })
            result.permission.forEach(e => {
              e.modify = !!e.modify
              e.view = !!e.view
            })
            result = JSON.stringify(result)
            this.permission = JSON.parse(result)
          }
          this.isLoading = false
        })
      },
      savePermission () {
        this.isLoading = true
        let id = this.permission.id
        savePermission(id, this.permission).then(e => {
          this.isLoading = false
        })
      }
    },
    mounted () {
      this.loadPermission(this.$route.params.id)
    }
  }
</script>



