<template>
<div class="row">
  <template>
    <div class="col-md-12">
      <div class="card" v-loading="isLoading">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.sale_of.all_sale_of') }}</h4>
        </div>
        <div class="card-content">
          <el-table :data="listSaleOf">
            <el-table-column type="index" min-width="70" width="70">

            </el-table-column>
            <el-table-column prop="name" :label="$t('app.sale_of.name')" min-width="200">
              <template slot-scope="props">
                <template v-if="props.row.isEdit">
                  <el-input v-model="props.row.name"></el-input>
                </template>
                <template v-else>
                  <span>{{ props.row.name }}</span>
                </template>
              </template>
            </el-table-column>
            <el-table-column :label="$t('app.sale_of.percent')"
                             min-width="100">
              <template slot-scope="props">
                <template v-if="props.row.isEdit">
                  <el-input-number v-model="props.row.percent" :min="1"></el-input-number>
                </template>
                <template v-else>
                  <span>{{ props.row.percent }} %</span>
                </template>
              </template>
            </el-table-column>
            <el-table-column :label="$t('app.sale_of.type')"
                             min-width="200">
              <template slot-scope="props">
                <template v-if="props.row.isEdit">
                  <el-select v-model="props.row.type">
                    <el-option
                        v-for="item in types"
                        :key="item.value"
                        :label="item.name"
                        :value="item.value">
                    </el-option>
                  </el-select>
                </template>
                <template v-else>
                  <span v-if="props.row.type === 'for_all'" class="label label-success">{{ $t('app.sale_of.for_all') }}</span>
                  <span v-if="props.row.type === 'for_product_type'" class="label label-primary">{{ $t('app.sale_of.for_product_type') }}</span>
                </template>
              </template>
            </el-table-column>
            <el-table-column :label="$t('app.status')" min-width="100">
              <template slot-scope="props">
                <template v-if="props.row.isEdit">
                  <el-switch v-model="props.row.active"
                  active-color="#13ce66"
                  inactive-color="#ff4949">
                  </el-switch>
                </template>
                <template v-else>
                  <span v-if="props.row.status === 'active'" class="label label-success">{{ $t('app.user.active') }}</span>
                  <span v-else class="label label-danger">{{ $t('app.user.unactive') }}</span>
                </template>
              </template>
            </el-table-column>
            <el-table-column :label="$t('app.action')" min-width="200">
              <template slot-scope="props">
                  <template v-if="props.row.isEdit">
                    <button class="btn btn-info btn-icon btn-fill btn-square" @click="handleCancel(props.$index, props.row)"><i class="fa fa-ban"></i></button>
                    <button class="btn btn-success btn-icon btn-fill btn-square" @click="handleSave(props.$index, props.row)"><i class="fa fa-floppy-o"></i></button>
                  </template>
                  <template v-else>
                    <a class="btn btn-warning btn-icon btn-fill btn-square" @click="handleEdit(props.$index, props.row)"><i class="fa fa-pencil-square-o"></i></a>
                    <a class="btn btn-danger btn-icon btn-fill btn-square" @click="handleDelete(props.$index, props.row)"><i class="fa fa-times"></i></a>
                  </template>

              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
  </template>
</div>
</template>

<script>
  import swal from 'sweetalert'
  import {getListSaleOf, deleteSaleOf, editSaleOf} from '@/api/saleof'
  export default {
    data () {
      return {
        isLoading: false,
        listSaleOf: [],
        isEditing: false,
        temp: {
          name: '',
          percent: 0,
          type: 'for_all',
          active: false
        },
        types: [
          {
            name: this.$t('app.sale_of.for_all'),
            value: 'for_all'
          },
          {
            name: this.$t('app.sale_of.for_product_type'),
            value: 'for_product_type'
          }
        ]
      }
    },
    mounted () {
      this.loadSaleOf()
    },
    methods: {
      loadSaleOf () {
        this.listSaleOf = []
        this.isLoading = true
        getListSaleOf().then(e => {
          if (e.data.is_valid) {
            this.listSaleOf = Object.assign([], e.data.sale_of)
          }
          this.isLoading = false
        })
      },
      handleEdit (index, row) {
        if (this.isEditing) {
          return true
        }
        row.isEdit = true
        this.temp.name = row.name
        this.temp.percent = row.percent
        this.temp.type = row.type
        this.temp.active = row.active
        this.isEditing = true
      },
      handleDelete (index, row) {
        let html = document.createElement('div')
        // let id = row.id
        html.innerHTML = this.$t('app.sale_of.delete', {name: row.name})
        swal({
          icon: 'warning',
          buttons: true,
          dangerMode: true,
          content: html
        }).then(isDelete => {
          if (isDelete) {
            this.isLoading = true
            deleteSaleOf(row.id).then(e => {
              if (e.data.status === 'success') {
                this.loadSaleOf()
              }
              this.isLoading = false
            })
          }
        })
      },
      handleCancel (index, row) {
        row.name = this.temp.name
        row.percent = this.temp.percent
        row.type = this.temp.type
        row.active = this.temp.active
        this.isEditing = false
        row.isEdit = false
      },
      handleSave (index, row) {
        this.isLoading = true
        let id = row.id
        editSaleOf(id, row).then(e => {
          if (e.data.status === 'success') {
            this.loadSaleOf()
          }
        })
      }
    }
  }
</script>



