<template>
  <div class="row">
    <div class="col-md-12" v-loading="isLoading">
      <div class="card">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.bill.bill_out') }}</h4>
        </div>
        <div class="card-content" v-if="'id' in bill">
          <div class="row">
            <div class="col-xs-6">
              <h4>#{{ bill.id }}</h4>
              <p>{{ $t('app.status') }}:
                <span v-if="bill.type === 'retail'" class="label label-success">{{ $t('app.sale.retail') }}</span>
                <span v-if="bill.type === 'wholesale'" class="label label-primary">{{ $t('app.sale.wholesale') }}</span>
              </p>
              <p>{{ $t('app.bill.saler') }}: <strong>{{ bill.user.name }}</strong></p>
              <p>{{ $t('app.sale.customer') }}: <strong>
                <span v-if="bill.customer.name === 'root'">
                  {{ $t('app.customer.root') }}
                </span>
                <span v-else>{{ bill.customer.name }}</span>
              </strong></p>
              <p>{{ $t('app.bill.sum_price') }}: <strong v-html="sumPrice"></strong></p>
              <p>{{ $t('app.bill.warehouse') }}: <strong>{{ bill.warehouse.name }}</strong></p>
              <p>{{ $t('app.bill.date_in') }}: <strong>{{ bill.created_at | moment('H:mm DD/MM/YYYY') }}</strong></p>
              <p>{{ $t('app.bill.date_update') }} <strong>{{ bill.updated_at | moment('H:mm DD/MM/YYYY') }}</strong></p>
              <p>{{ $t('app.bill.mode_sale') }}:</p>
              <p>
                <span v-if="isModifyPrice" class="label label-danger">{{ $t('app.sale.user_modify_price') }}</span>
                <span v-if="'name' in bill.sale_of && bill.sale_of.name !== 'default'" class="label label-default">
                  {{ bill.sale_of.name }}
                </span>
                <span v-if="bill.price_off >= 1" class="label label-danger">{{ $t('app.sale.sum_bill_sale_of') }}: {{ bill.price_off + $t('app.price_suffix') }}</span>
              </p>
              <div class="action-group">
                <button class="btn btn-info btn-square btn-fill" @click="printBill()"><i class="fa fa-print"></i> {{ $t('app.bill.print') }}</button>
                <button class="btn btn-danger btn-square btn-fill" @click="deleteBill"><i class="fa fa-times"></i> {{ $t('app.bill.delete') }}</button>
              </div>
            </div>
            <div class="col-xs-6">
              <h4>#{{ $t('app.bill.note') }}</h4>
              <textarea rows="4" v-model="bill.note" class="form-control" :disabled="isEditNote"></textarea>
              <div class="float-right">
                <template v-if="canModify || bill.user_id === currentUser.id">
                  <button class="btn btn-warning btn-square btn-fill" style="margin-top: 10px;" v-if="isEditNote" @click="isEditNote = false"><i class="fa fa-pencil"></i> {{ $t('app.bill.edit_note') }}</button>
                  <button class="btn btn-success btn-square btn-fill" style="margin-top: 10px;" v-else @click="saveNoteBill"><i class="fa fa-check"></i> {{ $t('app.bill.save_note') }}</button>
                </template>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.bill.detail_bill') }}</h4>
        </div>
        <div class="card-content" v-loading="isLoadingListProduct">
          <el-table :data="bill.product" show-summary :summary-method="getSummaries">
            <el-table-column type="index">

            </el-table-column>
            <el-table-column prop="product.name" min-width="200"
                             :label="$t('app.product.name')">
            </el-table-column>
            <el-table-column min-width="180"
                             :label="$t('app.product.size_color')">
              <template slot-scope="props">
                <template v-if="props.row.isEdit">
                  <el-select v-model="props.row.new_property_id">
                    <el-option
                        v-for="item in props.row.product.new_property"
                        :key="item.id"
                        :label="item.color_of.toUpperCase() + ' - '+ item.size_of.toUpperCase()"
                        :value="item.id">
                    </el-option>
                  </el-select>
                </template>
                <template v-else>
                  <span>{{ props.row.properties.toUpperCase() }}</span>
                </template>
              </template>
            </el-table-column>
            <el-table-column prop="count" min-width="100"
                             :label="$t('app.count')">
              <template slot-scope="props">
                <template v-if="props.row.isEdit">
                  <el-input-number v-model="props.row.count" :min="1"></el-input-number>
                </template>
                <template v-else>
                  <span>{{ props.row.count }}</span>
                </template>
              </template>
            </el-table-column>
            <el-table-column min-width="150"
                             :label="$t('app.product.price_out')">
              <template slot-scope="props">
                <template v-if="props.row.isEdit">
                  <money v-model="props.row.price" class="form-control"></money>
                </template>
                <template v-else>
                  <template v-if="props.row.product.price !== props.row.price">
                    <span style="color: red; text-decoration-line: line-through;">{{ $price(props.row.product.price) + $t('app.price_suffix') }}</span>
                    <span>{{ $price(props.row.price) + $t('app.price_suffix') }}</span>
                  </template>
                  <template v-else>
                    <span>{{ $price(props.row.price) + $t('app.price_suffix') }}</span>
                  </template>
                </template>
              </template>
            </el-table-column>
            <el-table-column :label="$t('app.product.price')" min-width="150" :formatter="formatPrice" v-if="!isEditing">

            </el-table-column>
            <el-table-column v-if="bill.user_id === currentUser.id || canModify" :label="$t('app.product.action')" min-width="100">
              <template slot-scope="props">
                <template v-if="props.row.isEdit">
                  <button class="btn btn-info btn-icon btn-fill btn-square" @click="handleCancel(props.$index, props.row)"><i class="fa fa-ban"></i></button>
                  <button class="btn btn-success btn-icon btn-fill btn-square" @click="handleSave(props.$index, props.row)"><i class="fa fa-floppy-o"></i></button>
                </template>
                <template v-else>
                  <a class="btn btn-warning btn-icon btn-fill btn-square" @click="handleReturn(props.$index, props.row)"><i class="fa fa-refresh"></i></a>
                  <a class="btn btn-danger btn-icon btn-fill btn-square" @click="handleDelete(props.$index, props.row)"><i class="fa fa-times"></i></a>
                </template>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {getBillOut, deleteBillOut, editDetailBill, deleteProductInBillOut, saveNoteBillOut, getPrintBill} from '@/api/bill'
  import swal from 'sweetalert'
  export default {
    data () {
      return {
        bill: {},
        isLoading: false,
        currentUser: this.$store.getters.user,
        canApprove: false,
        canModify: false,
        isModifyPrice: false,
        isEditing: false,
        saveTemp: {
          properties: '',
          count: 0,
          price: 0
        },
        isLoadingListProduct: false,
        isEditNote: true
      }
    },
    mounted () {
      this.getBill(this.$route.params.id)
    },
    methods: {
      getBill (id) {
        this.isLoading = true
        getBillOut(id).then(e => {
          if (e.data.is_valid) {
            this.bill = e.data.data
            if (e.data.can_modify) {
              this.canModify = true
            }
          }
          this.isLoading = false
        })
      },
      deleteBill () {
        swal({
          text: this.$t('app.bill.alert.delete'),
          icon: 'warning',
          buttons: true,
          dangerMode: true
        }).then(isOK => {
          if (isOK) {
            this.isLoading = true
            deleteBillOut(this.bill.id).then(e => {
              this.isLoading = false
              if (e.data.status === 'success') {
                this.$router.push({
                  name: 'bill_out'
                })
              }
            })
          }
        })
      },
      getSummaries (param) {
        let sums = []
        const {data} = param
        sums[1] = this.$t('app.sum')
        let sumCount = 0
        let sumPrice = 0
        data.forEach(e => {
          sumCount += e.count
          sumPrice += (e.count * e.price)
          if (e.price !== e.product.price) {
            this.isModifyPrice = true
          }
        })
        sums[3] = sumCount
        sums[5] = this.$price(sumPrice) + this.$t('app.price_suffix')
        return sums
      },
      handleDelete (index, row) {
        let id = row.id
        swal({
          text: this.$t('app.bill.alert.product'),
          icon: 'warning',
          buttons: true,
          dangerMode: true
        }).then(isOK => {
          if (isOK) {
            this.isLoadingListProduct = true
            deleteProductInBillOut(id).then(e => {
              if (e.data.status === 'success') {
                this.getBill(this.bill.id)
              }
              this.isLoadingListProduct = false
            })
          }
        })
      },
      formatPrice (row, column) {
        let sum = 0
        sum = row.count * row.price
        return this.$price(sum) + this.$t('app.price_suffix')
      },
      handleReturn (index, row) {
        if (this.isEditing) {
          return false
        }
        this.isEditing = true
        row.isEdit = true
        this.saveTemp.properties = row.properties
        this.saveTemp.count = row.count
        this.saveTemp.price = row.price
      },
      handleCancel (index, row) {
        row.properties = this.saveTemp.properties
        row.count = this.saveTemp.count
        row.price = this.saveTemp.price
        row.isEdit = false
        this.isEditing = false
      },
      handleSave (index, row) {
        if ('new_property_id' in row) {
          let data = Object.assign({}, row)
          data.warehouse_id = this.bill.warehouse_id
          delete data.product
          this.isLoadingListProduct = true
          editDetailBill(data).then(e => {
            if (e.data.status === 'success') {
              this.getBill(this.bill.id)
              this.isEditing = false
            }
            this.isLoadingListProduct = false
          })
        } else {
          this.$notify({
            message: this.$t('app.bill.notify_not_new_property'),
            type: 'danger'
          })
        }
      },
      saveNoteBill () {
        this.isLoading = true
        saveNoteBillOut(this.bill.id, this.bill.note).then(e => {
          if (e.data.status === 'success') {
            this.getBill(this.bill.id)
          }
          this.isLoading = false
        })
      },
      printBill () {
        this.isLoading = true
        getPrintBill(this.bill.id).then(e => {
          if (e.data.is_valid) {
            this.$htmlToPaper(e.data.data)
          }
          this.isLoading = false
        })
      }
    },
    computed: {
      sumPrice: function () {
        if ('id' in this.bill) {
          let sum = 0
          let beforeSum = 0
          this.bill.product.forEach(e => {
            sum += e.count * e.price
          })
          beforeSum = sum
          sum -= this.bill.price_of
          if (this.bill.price_off >= 1) {
            return `<span style="color: red; text-decoration-line: line-through;">${beforeSum + this.$t('app.price_suffix')}</span> -> <span>${this.$price(sum) + this.$t('app.price_suffix')}</span>`
          } else {
            return `<span>${this.$price(sum) + this.$t('app.price_suffix')}</span>`
          }
        } else {
          return ''
        }
      }
    }
  }
</script>



