<template>
  <div class="row">
    <div class="col-md-12">
      <div class="card" v-loading="isLoading" v-if="product.name">
        <div class="card-header">
          <h4 class="card-title">{{ product.name }}</h4>
        </div>
        <div class="card-content">
          <div class="detail-product">
            <p>{{ $t('app.product.string_id') }}: <strong>{{ product.string_id }}</strong></p>
            <p>{{ $t('app.product.unit') }}: <strong>{{ product.type === 'item' ? $t('app.product.type.item') : $t('app.product.type.package') }}</strong></p>
            <template v-if="'price_first' in product">
              <p>{{ $t('app.product.price_in') }}: <strong>{{ $price(product.price_first) + $t('app.price_suffix') }}</strong></p>
            </template>
            <p>{{ $t('app.product.price_out') }}: <strong>{{ $price(product.price) + $t('app.price_suffix') }}</strong></p>
            <p>{{ $t('app.status') }}:
              <template v-if="product.status === 'ready'">
                <span class="label label-success">{{ $t('app.product.status.ready') }}</span>
              </template>
              <template v-if="product.status === 'opened'">
                <span class="label label-info">{{ $t('app.product.status.opened') }}</span>
              </template>
              <template v-if="product.status === 'selled'">
                <span class="label label-danger">{{ $t('app.product.status.selled') }}</span>
              </template>
              <template v-if="product.status === 'locked'">
                <span class="label label-default">{{ $t('app.product.status.locked') }}</span>
              </template>
            </p>
            <p>{{ $t('app.sum_product') }}: <strong>{{ sumCountProperties }}</strong></p>
            <div class="action-group">
              <template v-if="(product.status === 'ready' || product.status === 'opened') && product.type === 'package' && canInputPackage">
                <router-link class="btn btn-primary btn-fill btn-square" :to="{name: 'in_package', params: {id: product.id}}"><i class="fa fa-keyboard-o"></i> {{ $t('app.package.input') }}</router-link>
              </template>
              <template v-if="canModify">
                <router-link :to="{name: 'edit_product', params: {id: product.id}}" class="btn btn-warning btn-fill btn-square"><i class="fa fa-pencil"></i> {{ $t('app.routes.edit_product') }}</router-link>
                <button class="btn btn-danger btn-fill btn-square" @click="deleteProduct(product.id)"><i class="fa fa-trash"></i> {{ $t('app.product.delete') }}</button>
              </template>
            </div>
          </div>
        </div>
      </div>
      <div class="card" v-if="'name' in product" v-loading="isLoadingProperty">
        <div class="card-header">
          <h4 class="card-title">
            <span>{{ $t('app.product.list_item') }}</span>
          </h4>
        </div>
        <div class="card-content">
          <div v-if="valueSelectTypeProduct === 'all'">
            <p-checkbox v-model="isViewDeletedProduct">{{ $t('app.product.view_deleted_product') }}</p-checkbox>
          </div>
          <template v-if="product.type === 'item'">
            <el-table :data="product.property">
              <el-table-column type="index">
              </el-table-column>
              <el-table-column :label="$t('app.product.color')" min-width="100">
                <template slot-scope="props">
                  <template v-if="props.row.isEdit">
                    <el-input :value="props.row.color_of"></el-input>
                  </template>
                  <template v-else>
                    <span>{{ props.row.color_of }}</span>
                  </template>
                </template>
              </el-table-column>
              <el-table-column prop="size_of" :label="$t('app.product.size')" min-width="100">
                <template slot-scope="props">
                  <template v-if="props.row.isEdit">
                    <el-input :value="props.row.size_of"></el-input>
                  </template>
                  <template v-else>
                    <span>{{ props.row.size_of }}</span>
                  </template>
                </template>
              </el-table-column>
              <el-table-column :label="$t('app.status')" min-width=150>
                <template slot-scope="props">
                  <template v-if="props.row.isEdit && props.row.is_from_package !== 0 && props.row.warehouse.name === 'root'"">
                    <el-select v-model="props.row.status">
                      <el-option
                          v-for="item in options"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value">
                      </el-option>
                    </el-select>
                  </template>
                  <template v-else>
                    <template v-if="props.row.status === 'ready'">
                      <span class="label label-success">{{ $t('app.package.status.ready') }}</span>
                    </template>
                    <template v-if="props.row.status === 'wash'">
                      <span class="label label-warning">{{ $t('app.package.status.wash') }}</span>
                    </template>
                    <template v-if="props.row.status === 'delete'">
                      <span class="label label-danger">{{ $t('app.package.status.delete') }}</span>
                    </template>
                    <template v-if="props.row.is_from_package !== 0">
                      <span class="label label-default">{{ $t('app.package.status.package') }}</span>
                    </template>
                  </template>
                </template>
              </el-table-column>
              <el-table-column :label="$t('app.count')" min-width="100">
                <template slot-scope="props">
                  <template v-if="props.row.isEdit">
                    <el-input-number v-model="props.row.count" :min="1"></el-input-number>
                  </template>
                  <template v-else>
                    <span>{{ props.row.count }}</span>
                  </template>
                </template>
              </el-table-column>
              <el-table-column :label="$t('app.warehouse.name')" min-width="150">
                <template slot-scope="props">
                  <template v-if="props.row.isEdit && props.row.status === 'ready' && false"> <!--ALWAYS FALSE-->
                    <select v-model="props.row.warehouse_id" class="form-control">
                      <template v-for="warehouse in listWarehouse">
                        <option :value="warehouse.id">
                            <span v-if="warehouse.name === 'root'">{{ $t('app.warehouse.root') }}</span>
                            <span v-else>{{ warehouse.name }}</span>
                        </option>
                      </template>
                    </select>
                  </template>
                  <template v-else>
                    <template v-if="props.row.status === 'ready'">
                      <span v-if="props.row.warehouse.name === 'root'">
                        {{ $t('app.warehouse.root') }}
                      </span>
                      <span v-else>{{ props.row.warehouse.name }}</span>
                    </template>
                  </template>
                </template>
              </el-table-column>
              <el-table-column :label="$t('app.action')" min-width="100" v-if="canModify">
                <template slot-scope="props">
                  <template v-if="props.row.isEdit">
                    <button class="btn btn-info btn-icon btn-fill btn-square" @click="handleCancel(props.row, props.$index)"><i class="fa fa-ban"></i></button>
                    <button class="btn btn-success btn-icon btn-fill btn-square" @click="handleSave(props.row, props.$index)"><i class="fa fa-floppy-o"></i></button>
                  </template>
                  <template v-else>
                    <button v-if="props.row.status === 'ready'" class="btn btn-success btn-icon btn-fill btn-square" @click="handleSwitchWarehouse(props.row, props.$index)"><i class="fa fa-share"></i></button>
                    <button class="btn btn-warning btn-icon btn-fill btn-square" @click="handleEdit(props.row, props.$index)"><i class="fa fa-pencil-square-o"></i></button>
                    <button class="btn btn-danger btn-icon btn-fill btn-square" @click="handleDelete(props.row, props.$index)"><i class="fa fa-times"></i></button>
                  </template>
                </template>
              </el-table-column>
            </el-table>
          </template>
          <!-- PROPERTY PRODUCT ITEM PACKAGE -->
          <template v-else>
            <el-table :data="product.package_item">
              <el-table-column type="index">
              </el-table-column>
              <el-table-column :label="$t('app.product.name')" min-width="150">
                <template slot-scope="props">
                    <span>{{ props.row.product.name }}</span>
                </template>
              </el-table-column>
              <el-table-column :label="$t('app.product.size')" min-width="100">
                <template slot-scope="props">
                  <template v-if="props.row.isEdit">
                    <el-input :value="props.row.size_of"></el-input>
                  </template>
                  <template v-else>
                    <span>{{ props.row.size_of }}</span>
                  </template>
                </template>
              </el-table-column>
              <el-table-column :label="$t('app.product.color')" min-width="100">
                <template slot-scope="props">
                  <template v-if="props.row.isEdit">
                    <el-input :value="props.row.color_of"></el-input>
                  </template>
                  <template v-else>
                    <span>{{ props.row.color_of }}</span>
                  </template>
                </template>
              </el-table-column>
              <el-table-column :label="$t('app.status')" min-width=150>
                <template slot-scope="props">
                  <template v-if="props.row.isEdit && props.row.warehouse.name === 'root'">
                    <el-select v-model="props.row.status">
                      <el-option
                          v-for="item in options"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value">
                      </el-option>
                    </el-select>
                  </template>
                  <template v-else>
                    <template v-if="props.row.status === 'ready'">
                      <span class="label label-success">{{ $t('app.package.status.ready') }}</span>
                    </template>
                    <template v-if="props.row.status === 'wash'">
                      <span class="label label-warning">{{ $t('app.package.status.wash') }}</span>
                    </template>
                    <template v-if="props.row.status === 'delete'">
                      <span class="label label-danger">{{ $t('app.package.status.delete') }}</span>
                    </template>
                    <template v-if="props.row.is_from_package !== 0">
                      <span class="label label-default">{{ $t('app.package.status.package') }}</span>
                    </template>
                  </template>
                </template>
              </el-table-column>
              <el-table-column :label="$t('app.count')" min-width="100">
                <template slot-scope="props">
                  <template v-if="props.row.isEdit">
                    <el-input-number v-model="props.row.count" :min="1"></el-input-number>
                  </template>
                  <template v-else>
                    <span>{{ props.row.count }}</span>
                  </template>
                </template>
              </el-table-column>
              <el-table-column :label="$t('app.warehouse.name')" min-width="150">
                <template slot-scope="props">
                  <template v-if="props.row.isEdit && props.row.status === 'ready' && false">
                    <select v-model="props.row.warehouse_id" class="form-control">
                      <template v-for="warehouse in listWarehouse">
                        <option :value="warehouse.id">
                            <span v-if="warehouse.name === 'root'">{{ $t('app.warehouse.root') }}</span>
                            <span v-else>{{ warehouse.name }}</span>
                        </option>
                      </template>
                    </select>
                  </template>
                  <template v-else>
                    <template v-if="props.row.status === 'ready'">
                      <span v-if="props.row.warehouse.name === 'root'">
                        {{ $t('app.warehouse.root') }}
                      </span>
                      <span v-else>{{ props.row.warehouse.name }}</span>
                    </template>
                  </template>
                </template>
              </el-table-column>
              <el-table-column :label="$t('app.action')" min-width="100" v-if="canModify">
                <template slot-scope="props">
                  <template v-if="props.row.isEdit">
                    <button class="btn btn-info btn-icon btn-fill btn-square" @click="handleCancel(props.row, props.$index)"><i class="fa fa-ban"></i></button>
                    <button class="btn btn-success btn-icon btn-fill btn-square" @click="handleSave(props.row, props.$index)"><i class="fa fa-floppy-o"></i></button>
                  </template>
                  <template v-else>
                    <button class="btn btn-success btn-icon btn-fill btn-square" v-if="props.row.status === 'ready'" @click="handleSwitchWarehouse(props.row, props.$index)"><i class="fa fa-share"></i></button>
                    <button class="btn btn-warning btn-icon btn-fill btn-square" @click="handleEdit(props.row, props.$index)"><i class="fa fa-pencil-square-o"></i></button>
                    <button class="btn btn-danger btn-icon btn-fill btn-square" @click="handleDelete(props.row, props.$index)"><i class="fa fa-times"></i></button>
                  </template>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import swal from 'sweetalert'
  import {loadListPropertiesProduct, saveProperty, deleteProperty, deleteProduct} from '@/api/product'
  import {getListWarehouse, switchWarehouse} from '@/api/warehouse'
  export default {
    data () {
      return {
        product: {},
        isLoading: false,
        canModify: false,
        canInputPackage: false,
        options: [
          {
            value: 'ready',
            label: this.$t('app.package.status.ready')
          },
          {
            value: 'wash',
            label: this.$t('app.package.status.wash')
          },
          {
            value: 'delete',
            label: this.$t('app.package.status.delete')
          }
        ],
        isEdit: false,
        isLoadingProperty: false,
        tempProperty: {
          sizeOf: '',
          colorOf: '',
          status: '',
          count: 0,
          warehouse_id: 0,
          warehouse_name: ''
        },
        listWarehouse: [],
        isViewDeletedProduct: true,
        tempOfProduct: {}
      }
    },
    mounted () {
      this.loadListPropertiesProduct(this.$route.params.id)
      getListWarehouse().then(e => {
        if (e.data.is_valid) {
          this.listWarehouse = e.data.warehouse
        }
      })
    },
    methods: {
      loadListPropertiesProduct (id) {
        this.isLoading = true
        this.isLoadingProperty = true
        loadListPropertiesProduct(id, this.selectedWarehouse, this.valueSelectTypeProduct).then(e => {
          this.tempOfProduct = Object.assign({}, e.data.data)
          this.product = e.data.data
          this.isLoading = false
          this.isLoadingProperty = false
          if (e.data.can_modify) {
            this.canModify = true
          } else {
            this.canModify = false
          }
          if (e.data.can_input_package) {
            this.canInputPackage = true
          } else {
            this.canInputPackage = false
          }
        })
      },
      handleEdit (row, index) {
        let arr = []
        let isEdit = false
        arr.push(...this.product.property)
        arr.push(...this.product.package_item)
        arr.forEach(e => {
          if (e.isEdit === true) {
            isEdit = true
          }
        })
        if (!isEdit) {
          this.tempProperty.sizeOf = row.size_of
          this.tempProperty.colorOf = row.color_of
          this.tempProperty.count = row.count
          this.tempProperty.status = row.status
          this.tempProperty.warehouse_id = row.warehouse_id
          this.tempProperty.warehouse_name = row.warehouse.name
          row.isEdit = true
        }
      },
      handleDelete (row, index) {
        let html = document.createElement('div')
        let id = row.id
        html.innerHTML = this.$t('app.product.delete_property')
        swal({
          icon: 'warning',
          buttons: true,
          dangerMode: true,
          content: html
        }).then(isOK => {
          if (isOK) {
            deleteProperty(id).then(e => {
              if (e.data.status === 'success') {
                this.loadListPropertiesProduct(this.product.id)
              }
            })
          }
        })
      },
      handleSave (row, index) {
        this.isLoadingProperty = true
        saveProperty(row).then(e => {
          if (e.data.status !== 'success') {
            this.tempProperty.sizeOf = row.size_of
            this.tempProperty.colorOf = row.color_of
            this.tempProperty.count = row.count
            this.tempProperty.status = row.status
            this.tempProperty.warehouse_id = row.warehouse_id
            this.tempProperty.warehouse_name = row.warehouse.name
          }
          this.listWarehouse.forEach(e => {
            if (e.id === row.warehouse_id) {
              row.warehouse.name = e.name
            }
          })
          this.isLoadingProperty = false
          row.isEdit = false
        })
      },
      handleCancel (row, index) {
        row.size_of = this.tempProperty.sizeOf
        row.color_of = this.tempProperty.colorOf
        row.count = this.tempProperty.count
        row.status = this.tempProperty.status
        row.warehouse_id = this.tempProperty.warehouse_id
        row.warehouse.name = this.tempProperty.warehouse_name
        row.isEdit = false
      },
      deleteProduct (id) {
        let html = document.createElement('div')
        html.innerHTML = this.$t('app.product.delete_alert', {name: this.product.name})
        swal({
          icon: 'warning',
          buttons: true,
          dangerMode: true,
          content: html
        }).then(isOk => {
          if (isOk) {
            this.isLoading = true
            deleteProduct(id).then(e => {
              if (e.data.status === 'success') {
                this.$router.push({name: 'product'})
              }
              this.isLoading = false
            })
          }
        })
      },
      handleSwitchWarehouse (row, index) {
        let wrapper = document.createElement('div')
        let divSelect = document.createElement('div')
        divSelect.className = 'form-group'
        let select = document.createElement('select')
        let labelSelect = document.createElement('label')
        labelSelect.innerText = this.$t('app.warehouse.select')
        labelSelect.style.float = 'left'
        select.className = 'form-control'
        let option
        this.listWarehouse.forEach(e => {
          if (e.id === row.warehouse_id) {
            return
          }
          option = document.createElement('option')
          option.value = e.id
          if (e.name === 'root') {
            option.text = this.$t('app.warehouse.root')
          } else {
            option.text = e.name
          }
          select.appendChild(option)
        })
        divSelect.appendChild(labelSelect)
        divSelect.appendChild(select)
        let divCount = document.createElement('div')
        let inputCount = document.createElement('input')
        let labelCount = document.createElement('label')
        labelCount.innerText = this.$t('app.product.count_need')
        labelCount.style.float = 'left'
        divCount.className = 'form-group'
        inputCount.className = 'form-control'
        inputCount.type = 'number'
        inputCount.value = row.count
        inputCount.onkeyup = function (e) {
          if (e.target.value > row.count) {
            e.target.value = row.count
          }
        }
        inputCount.onchange = function (e) {
          if (e.target.value > row.count) {
            e.target.value = row.count
          }
        }
        divCount.appendChild(labelCount)
        divCount.appendChild(inputCount)
        wrapper.appendChild(divSelect)
        wrapper.appendChild(divCount)
        swal({
          content: wrapper,
          buttons: true
        }).then(isOK => {
          if (isOK) {
            this.isLoadingProperty = true
            switchWarehouse(row.id, select.value, inputCount.value).then(e => {
              if (e.data.status === 'success') {
                this.loadListPropertiesProduct(this.$route.params.id)
              }
              this.isLoadingProperty = false
            })
          }
        })
      }
    },
    computed: {
      sumCountProperties () {
        let sum = 0
        if (this.product.type === 'item') {
          this.product.property.forEach(e => {
            sum += e.count
          })
        } else {
          this.product.package_item.forEach(e => {
            sum += e.count
          })
        }
        return sum
      },
      selectedWarehouse () {
        return this.$store.getters.selectedWarehouse
      },
      valueSelectTypeProduct: function () {
        return this.$store.getters.valueSelectTypeProduct
      }
    }
  }
</script>



