<template>
<div class="row" v-if="isShow">
    <div class="col-lg-6 col-md-12" v-loading="isLoading">
      <div class="card">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.product.add_product') }}</h4>
        </div>
        <vue-tabs class="card-content">
          <v-tab :title="$t('app.product.add_existed')">
            <div class="row">
              <div class="col-md-12">
                <form @submit.prevent="existProduct">
                  <label>{{ $t('app.product.select') }}</label>
                  <div class="form-group input-group">
                      <input type="input" class="form-control" :value="inputSelectProduct" :placeholder="$t('app.product.select')" disabled>
                      <span class="input-group-btn">
                        <button class="btn btn-fill btn-danger" type="button" @click="showDialog('product')">Chọn</button>
                      </span>
                  </div>
                  <div class="row" v-for="(index, item) in countProperty">
                    <div class="col-xs-4 col-sm-5 col-md-5">
                      <div class="form-group">
                        <label>{{ $t('app.product.color') }}</label>
                        <input type="text" :placeholder="$t('app.product.color')" name="list_color[]" class="form-control" required>
                      </div>
                    </div>
                    <div class="col-xs-4 col-sm-5 col-md-4">
                      <div class="form-group">
                        <label>{{ $t('app.product.size') }}</label>
                        <input type="text" :placeholder="$t('app.product.size')" name="list_size[]" class="form-control" required>
                      </div>
                    </div>
                    <div class="col-xs-4 col-sm-2 col-md-3">
                      <div class="form-group">
                        <label>{{ $t('app.count') }}</label>
                        <input type="number" :placeholder="$t('app.count')" name="list_count[]" class="form-control" required>
                      </div>
                    </div>
                  </div>
                  <div>
                    <button type="button" class="btn btn-wd btn-success btn-sm" @click.prevent="++countProperty">
                      <span class="btn-label"><i class="fa fa-plus"></i></span>
                      {{ $t('app.product.add_property') }}
                    </button>
                    <button type="button" class="btn btn-wd btn-danger btn-sm" :disabled="countProperty === 0" @click.prevent="--countProperty">
                      <span class="btn-label"><i class="fa fa-close"></i></span>
                      {{ $t('app.product.delete_property') }}
                    </button>
                  </div>
                  <div class="float-right">
                      <input type="hidden" name="action" value="exist">
                      <input type="hidden" name="type" value="item">
                      <input type="hidden" name="product_id" :value="idSelectProduct">
                      <input type="hidden" name="bill_id" :value="billId">
                    <button type="submit" class="btn btn-fill btn-sm btn-success btn-wd" :disabled="idSelectProduct === null || countProperty <= 0">{{ $t('app.save') }}</button>
                  </div>
                </form>
              </div>
            </div>
          </v-tab>
          <v-tab :title="$t('app.product.add_new')">
            <div class="row">
              <div class="col-md-12">
                <form @submit.prevent="productSubmit">
                    <div class="form-group">
                      <label>{{$t('app.product.type_in')}}</label>
                      <br>
                      <p-radio v-model="enabledRadio" label="1" inline>{{ $t('app.product.type.item') }}</p-radio>
                      <p-radio v-model="enabledRadio" label="2" inline>{{ $t('app.product.type.package') }}</p-radio>
                    </div>
                    <div class="form-group">
                      <label>{{ $t('app.product.string_id') }}</label>
                      <input v-validate="'required|alpha_dash|unique_string_id'" type="text" :placeholder="$t('app.product.string_id')" name="string_id" v-model="form.string_id" :data-vv-as="$t('app.product.string_id')" class="form-control">
                      <span class="text-danger">{{ errors.first('string_id') }}</span>
                    </div>
                    <div class="form-group">
                      <label>{{ $t('app.product.name') }}</label>
                      <input v-validate="'required|unique_name'" v-model="form.name" :data-vv-as="$t('app.product.name')" type="text" :placeholder="$t('app.product.name')" name="name" class="form-control">
                      <span class="text-danger">{{ errors.first('name') }}</span>
                    </div>
                    <div class="form-group">
                      <label>{{ $t('app.product.price_in') }}</label>
                      <money v-model="price_in" class="form-control" @input="changePriceOut"></money>
                    </div>
                    <div class="form-group">
                      <label>{{ $t('app.product.price_out') }}</label>
                      <money v-model="price_out" class="form-control"></money>
                      <p-checkbox v-model="isAutoPercent">{{$t('app.product.is_auto_percent')}}</p-checkbox>
                      <el-slider v-if="isAutoPercent" class="slider-danger" v-model="sliders.price_out"></el-slider>
                    </div>

                    <!-- PROPERTIES PRODUCT HERE -->
                    <template v-if="enabledRadio === '1'">
                    <!-- SELECT -->
                    <label>{{ $t('app.product.product_type') }}</label>
                    <div class="form-group input-group">
                        <input type="input" class="form-control" :value="inputSelectProductType"  disabled>
                        <span class="input-group-btn">
                          <button class="btn btn-fill btn-danger" type="button" @click="showDialog('product_type')">Chọn</button>
                        </span>
                    </div>
                    <label>{{ $t('app.product.supplier') }}</label>
                    <div class="form-group input-group">
                        <input type="input" class="form-control"  :value="inputSelectSupplier" disabled>
                        <span class="input-group-btn">
                          <button class="btn btn-fill btn-danger" type="button" @click="showDialog('supplier')">Chọn</button>
                        </span>
                    </div>
                    <input type="hidden" name="supplier_id" :value="idSelectSupplier">
                    <input type="hidden" name="product_type_id" :value="idSelectProductType">
                    <!-- END SELECT -->
                    <div class="row" v-for="(index, item) in countProperty">
                      <div class="col-xs-4 col-sm-5 col-md-5">
                        <div class="form-group">
                          <label>{{ $t('app.product.color') }}</label>
                          <input type="text" :placeholder="$t('app.product.color')" name="list_color[]" class="form-control" required>
                        </div>
                      </div>
                      <div class="col-xs-4 col-sm-5 col-md-4">
                        <div class="form-group">
                          <label>{{ $t('app.product.size') }}</label>
                          <input type="text" :placeholder="$t('app.product.size')" name="list_size[]" class="form-control" required>
                        </div>
                      </div>
                      <div class="col-xs-4 col-sm-2 col-md-3">
                        <div class="form-group">
                          <label>{{ $t('app.count') }}</label>
                          <input type="number" :placeholder="$t('app.count')" name="list_count[]" class="form-control" required>
                        </div>
                      </div>
                    </div>
                    <div>
                      <button type="button" class="btn btn-wd btn-success btn-sm" @click.prevent="++countProperty">
                        <span class="btn-label"><i class="fa fa-plus"></i></span>
                        {{ $t('app.product.add_property') }}
                      </button>
                      <button type="button" class="btn btn-wd btn-danger btn-sm" :disabled="countProperty === 0" @click.prevent="--countProperty">
                        <span class="btn-label"><i class="fa fa-close"></i></span>
                        {{ $t('app.product.delete_property') }}
                      </button>
                    </div>
                    </template>
                    <div class="float-right">
                      <!-- HERE IS HIDDEN FORM HAHA -->
                      <input type="hidden" name="action" value="new">
                      <input type="hidden" name="bill_id" :value="billId">
                      <input type="hidden" name="type" :value="enabledRadio === '1' ? 'item' : 'package'">
                      <!-- price_first is price when sale product -->
                      <input type="hidden" name="price_first" :value="price_in">
                      <input type="hidden" name="price" :value="price_out">
                      <button type="submit" class="btn btn-fill btn-wd btn-sm btn-success">{{ $t('app.product.save') }}</button>
                    </div>
                </form>
              </div>
            </div>
          </v-tab>
        </vue-tabs>
      </div>
    </div>
    <div class="col-lg-6 col-md-12" v-loading="isListProductLoading">
      <div class="card" v-if="listMoreBill.length >= 1">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.product.list_bill') }}</h4>
        </div>
        <div class="card-content">
          <el-table :data="listMoreBill" :row-class-name="tableRowClassName" show-summary :summary-method="getSummaries">
            <el-table-column type="index">

            </el-table-column>
            <el-table-column prop="product.name" min-width="200"
                             :label="$t('app.product.name')">
              <template slot-scope="props">
                <template v-if="props.row.product.type === 'package'">
                  <span class="label label-danger">{{ props.row.product.name }}</span>
                </template>
                <template v-else>
                  <span class="label label-info">{{ props.row.product.name }}</span>
                </template>
              </template>
            </el-table-column>
            <el-table-column prop="count" min-width="100"
                             :label="$t('app.count')">
            </el-table-column>
            <el-table-column prop="price_format" min-width="100"
                             :label="$t('app.product.price_in')">
            </el-table-column>
            <el-table-column prop="sum_format" min-width="150"
                             :label="$t('app.product.price')">
            </el-table-column>
            <el-table-column
              :label="$t('app.product.action')" min-width="100">
              <template slot-scope="props">
                <a class="btn btn-danger btn-xs btn-icon btn-fill btn-square btn-sm"  @click="handleDelete(props.$index, props.row)"><i class="fa fa-times"></i></a>
              </template>
            </el-table-column>
          </el-table>
          <div class="wrapper-note">
            <p-checkbox v-model="note.is_note">{{ $t('app.bill.note') }}</p-checkbox>
            <div class="row" v-if="note.is_note">
              <div class="col-xs-6">
                <textarea v-model="note.content" rows="3" :placeholder="$t('app.bill.input_note')" class="form-control"></textarea>
              </div>
            </div>
          </div>
          <div class="float-right" style="margin-top: 10px;">
              <button class="btn btn-warning btn-sm btn-fill btn-wd">{{ $t('app.cancel') }}</button>
              <button class="btn btn-success btn-sm btn-fill btn-wd" @click="saveBill">{{ $t('app.save') }}</button>
          </div>
        </div>
      </div>
    </div>
    <el-dialog
      :title="dialog.title"
      :visible.sync="dialogVisible"
      width="40%">
      <div class="row">
        <div class="col-xs-12">
          <div class="form-group">
            <label>{{ dialog.titleSearch }}</label>
            <input type="text" @input="inputSearch" v-model="searchString" class="form-control"
            :placeholder="dialog.titleSearch">
            <div style="max-height: 400px; overflow: auto;" v-loading="loadingTable">
              <el-table
                ref="singleTable"
                :data="listData"
                highlight-current-row
                @current-change="handleCurrentChange"
                style="width: 100%">
                <el-table-column
                  type="index"
                  width="100">
                </el-table-column>
                <el-table-column v-if="searchType === 'product'"
                  property="string_id" :label="$t('app.product.string_id')">

                </el-table-column>
                <el-table-column
                  property="name"
                  :label="titleHeaderTable"
                  min-width="100">
                </el-table-column>
                <el-table-column v-if="searchType === 'supplier'"
                  property="phone"
                  :label="$t('app.supplier.phone')"
                  min-width="150">
                </el-table-column>
                <el-table-column property="product_count" label="Count" min-width="80">

                </el-table-column>
              </el-table>
            </div>
          </div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <div @click="dialogVisible = false" class="btn btn-sm btn-primary">{{ $t('app.cancel') }}</div>
        <div class="btn btn-sm btn-warning" @click="rootSelected" v-show="visiableBtnRootSelect">{{ $t('app.supplier.root') }}</div>
        <div @click="confirmSelected" class="btn btn-sm btn-danger" :disabled="currentSelected === null">{{ $t('app.confirm') }}</div>
      </span>
    </el-dialog>
  </div>
</template>
<script>
  import Vue from 'vue'
  import VueTabs from 'vue-nav-tabs'
  import swal from 'sweetalert'
  import {getBillIn, submitProduct, getBillWithId, deleteMoreBill, uniqueProduct, searchProduct, saveBill} from '@/api/product'
  import {searchProductType} from '@/api/product-type'
  import {searchSupplier} from '@/api/supplier'
  Vue.use(VueTabs)


  export default {
    data () {
      return {
        price_in: 0,
        price_out: 0,
        enabledRadio: '1',
        sliders: {
          price_out: 20
        },
        isAutoPercent: false,
        countProperty: 0,
        billId: null,
        isLoading: true,
        isShow: true,
        listMoreBill: [],
        isListProductLoading: false,
        form: {
          name: '',
          string_id: ''
        },
        dialogVisible: false,
        dialog: {
          title: ''
        },
        searchString: '',
        listData: [],
        searchType: 'supplier',
        currentSelected: null,
        loadingTable: false,
        inputSelectSupplier: this.$t('app.supplier.root'),
        inputSelectProductType: this.$t('app.supplier.root'),
        idSelectProductType: 1,
        idSelectSupplier: 1,
        idSelectProduct: null,
        visiableBtnRootSelect: true,
        titleHeaderTable: '',
        inputSelectProduct: '',
        note: {
          is_note: false,
          content: ''
        }
      }
    },
    methods: {
      changePriceOut () {
        if (this.isAutoPercent) {
          this.price_out = (this.price_in / 100 * this.sliders.price_out) + this.price_in
        }
      },
      productSubmit (e) {
        if (!this.formValid) {
          return false
        }
        this.isLoading = true
        let formData = new FormData(e.target)
        let form = e.target
        submitProduct(formData).then(e => {
          if (e.data.status === 'success') {
            form.reset()
            this.form.string_id = ''
            this.form.name = ''
            this.countProperty = 1
            this.loadBIll()
          }
          this.isLoading = false
        })
      },
      loadFirstData () {
        this.isShow = true
        if (this.$route.params.param === 'new') {
          getBillIn().then(e => {
            if (e.data.is_valid) {
              this.billId = e.data.id
              this.isLoading = false
            }
            this.listMoreBill = []
          })
        } else {
          this.loadBIll()
          this.isLoading = false
        }
      },
      loadBIll () {
        let bill
        if (this.$route.params.param === 'new') {
          bill = this.billId
        } else {
          bill = this.$route.params.param
        }
        this.isListProductLoading = true
        getBillWithId(bill).then(e => {
          if (e.data.is_valid) {
            this.billId = e.data.id
            this.listMoreBill = e.data.bill.product
          } else {
            this.isShow = false
          }
          this.isListProductLoading = false
        })
      },
      tableRowClassName ({row, index}) {
        if (row.product.type === 'package') {
          return 'u-package-list'
        }
      },
      handleDelete (index, row) {
        swal({
          text: this.$t('app.product.delete_more_bill'),
          icon: 'warning',
          buttons: true,
          dangerMode: true
        }).then(isDelete => {
          if (isDelete) {
            this.isListProductLoading = true
            deleteMoreBill(row.id).then(e => {
              if (e.data.status === 'success') {
                this.loadBIll()
              }
            })
          }
        })
      },
      getSummaries (param) {
        let sums = []
        const {data} = param
        sums[1] = this.$t('app.sum')
        let sumCount = 0
        let sumPrice = 0
        data.forEach(e => {
          sumCount += e.count
          sumPrice += e.sum
        })
        sums[2] = sumCount
        sums[4] = this.$price(sumPrice)
        return sums
      },
      showDialog (type) {
        this.dialogVisible = true
        this.searchString = ''
        this.currentSelected = null
        this.listData = []
        this.visiableBtnRootSelect = true
        if (type === 'supplier') {
          this.dialog.title = this.$t('app.routes.supplier')
          this.dialog.titleSearch = this.$t('app.title_search', {name: this.$t('app.routes.supplier')})
          this.searchType = 'supplier'
          this.titleHeaderTable = this.$t('app.supplier.name')
        } else if (type === 'product_type') {
          this.dialog.title = this.$t('app.routes.product_type')
          this.dialog.titleSearch = this.$t('app.title_search', {name: this.$t('app.routes.product_type')})
          this.searchType = 'product_type'
          this.titleHeaderTable = this.$t('app.product_type.name')
        } else {
          this.dialog.title = this.$t('app.routes.product')
          this.dialog.titleSearch = this.$t('app.title_search', {name: this.$t('app.routes.product')})
          this.searchType = 'product'
          this.visiableBtnRootSelect = false
          this.titleHeaderTable = this.$t('app.product.name')
        }
      },
      inputSearch (e) {
        let value = e.target.value
        let getSearch
        setTimeout(() => {
          if (this.searchString === value) {
            this.loadingTable = true
            if (this.searchType === 'supplier') {
              getSearch = searchSupplier(value)
            } else if (this.searchType === 'product_type') {
              getSearch = searchProductType(value)
            } else {
              getSearch = searchProduct(value)
            }
            getSearch.then(e => {
              this.listData = []
              if (e.data.is_valid) {
                this.listData = e.data.data
              }
              this.loadingTable = false
            })
          }
        }, 500)
      },
      handleCurrentChange (row) {
        this.currentSelected = row
      },
      confirmSelected () {
        if (this.searchType === 'supplier') {
          this.inputSelectSupplier = this.currentSelected.name
          this.idSelectSupplier = this.currentSelected.id
        } else if (this.searchType === 'product_type') {
          this.inputSelectProductType = this.currentSelected.name
          this.idSelectProductType = this.currentSelected.id
        } else {
          this.inputSelectProduct = this.currentSelected.name
          this.idSelectProduct = this.currentSelected.id
        }
        this.dialogVisible = false
      },
      rootSelected () {
        if (this.searchType === 'supplier') {
          this.idSelectSupplier = 1
          this.inputSelectSupplier = this.$t('app.supplier.root')
        } else {
          this.idSelectProductType = 1
          this.inputSelectProductType = this.$t('app.supplier.root')
        }
        this.dialogVisible = false
      },
      existProduct (e) {
        this.isLoading = true
        let formData = new FormData(e.target)
        let form = e.target
        submitProduct(formData).then(e => {
          if (e.data.status === 'success') {
            form.reset()
            this.loadBIll()
          }
          this.isLoading = false
        })
      },
      saveBill () {
        this.isListProductLoading = true
        saveBill(this.billId, this.note).then(e => {
          if (e.data.status === 'success') {
            this.$router.push('/admin/in-product/new')
            this.loadFirstData()
          }
        })
      }
    },
    mounted () {
      this.loadFirstData()
      let isUniqueId = (value) =>
        new Promise((resolve) => {
          setTimeout(() => {
            if (value !== this.form.string_id) {
              return resolve({
                valid: false
              })
            }
            uniqueProduct(value, 'string_id').then(e => {
              if (e.data.is_valid) {
                return resolve({
                  valid: true
                })
              } else {
                return resolve({
                  valid: false
                })
              }
            })
          }, 350)
        })

      let isUniqueName = (value) => new Promise(resolve => {
        setTimeout(() => {
          if (value !== this.form.name) {
            return resolve({
              valid: false
            })
          }
          uniqueProduct(value, 'name').then(e => {
            if (e.data.is_valid) {
              return resolve({
                valid: true
              })
            } else {
              return resolve({
                valid: false
              })
            }
          })
        }, 350)
      })

      this.$validator.extend('unique_string_id', {
        validate: isUniqueId,
        getMessage: (field, params) => {
          return this.$t('app.unique', {name: field})
        }
      })
      this.$validator.extend('unique_name', {
        validate: isUniqueName,
        getMessage: (field, params) => {
          return this.$t('app.unique', {name: field})
        }
      })
    },
    watch: {
      '$route.params.param': function () {
        this.loadFirstData()
      },
      listMoreBill () {
        this.listMoreBill.forEach(e => {
          e['sum'] = e.product.price_first * e.count
          e['sum_format'] = this.$price(e['sum']) + this.$t('app.price_suffix')
          e['price_format'] = this.$price(e.product.price_first) + this.$t('app.price_suffix')
        })
      }
    },
    computed: {
      formValid () {
        return Object.keys(this.fields).every(field => {
          return this.fields[field] && this.fields[field].valid
        })
      }
    }
  }
</script>

<style scoped>
  form {
    text-align: left;
  }
  .u-btn-delete-properties {
    color: red;
    cursor: pointer;
  }
  .u-padding-properties {
    margin-top: 37px;
    margin-left: -2%;
  }
  .label {
    border-radius: unset !important;
    text-transform: unset !important;
  }
  .input-group-btn > button {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
  }
</style>



