<template>
  <div>
    <nav class="navbar navbar-transparent navbar-absolute">
      <div class="container">
        <div class="navbar-header">
          <button type="button" class="navbar-toggle"
                  data-toggle="collapse"
                  data-target="#navigation-example-2"
                  @click="toggleNavbar">
            <span class="sr-only">Toggle navigation</span>
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
          </button>
          <router-link class="navbar-brand" to="/admin">{{ $t('app.title') }}</router-link>
        </div>
        <div class="collapse navbar-collapse">
          <ul class="nav navbar-nav navbar-right">
            <router-link to="/admin/overview" tag="li">
              <a>{{ $t('app.dashboard') }}</a>
            </router-link>
          </ul>
        </div>
      </div>
    </nav>

    <div class="wrapper wrapper-full-page">
      <div class="full-page login-page" data-color="blue"
           data-image="static/img/background/background-6.jpg">
        <!--   you can change the color of the filter page using: data-color="blue | azure | green | orange | red | purple" -->
        <div class="content">
          <div class="container">
            <div class="row">
              <div class="col-md-4 col-sm-6 col-md-offset-4 col-sm-offset-3">
                <form @submit.prevent="login">
                  <div class="card" data-background="color" data-color="blue">
                    <div class="card-header">
                      <h3 class="card-title">{{ $t('login.header') }}</h3>
                    </div>
                    <div class="card-content">
                      <div class="form-group">
                        <label>{{ $t('login.username') }}</label>
                        <input type="text" :placeholder="$t('login.username')" v-model="form.username" class="form-control input-no-border" :disabled="isDisable">
                      </div>
                      <div class="form-group">
                        <label>{{ $t('login.password') }}</label>
                        <input type="password" :placeholder="$t('login.password')" v-model="form.password" class="form-control input-no-border" :disabled="isDisable">
                      </div>
                    </div>
                    <div class="card-footer text-center">
                      <button type="submit" class="btn btn-fill btn-wd" :disabled="isDisable">{{ $t('login.btn.login') }}</button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
        <div class="full-page-background" style="background-image: url(static/img/background/background-6.jpg) "></div>
      </div>
    </div>
    <div class="collapse navbar-collapse off-canvas-sidebar">
      <ul class="nav nav-mobile-menu">
        <router-link to="/admin/overview" tag="li">
          <a>{{ $t('app.dashboard') }}</a>
        </router-link>
      </ul>
    </div>
  </div>
</template>
<script>
  export default {
    methods: {
      toggleNavbar () {
        document.body.classList.toggle('nav-open')
      },
      closeMenu () {
        document.body.classList.remove('nav-open')
        document.body.classList.remove('off-canvas-sidebar')
      },
      login () {
        this.isDisable = true
        if (this.form.username === '' || this.form.password === '') {
          this.$notify({
            message: this.$t('login.not_empty'),
            icon: this.$t('icon.warning'),
            type: 'warning'
          })
          this.isDisable = false
          return false
        }
        this.$store.dispatch('login', this.form).then(e => {
          if (e.data.status === 'success') {
            this.$router.push({path: '/admin'})
          }
          this.isDisable = false
        }).catch(() => {
          this.isDisable = false
        })
      }
    },
    beforeDestroy () {
      this.closeMenu()
    },
    data () {
      return {
        form: {
          username: '',
          password: ''
        },
        isDisable: false
      }
    }
  }
</script>
<style>
</style>



