import request from '@/utils/request'

export function getListBillIn (index) {
  return request({
    url: '/bill-in/all?page=' + index,
    method: 'get'
  })
}

export function approveBill (id) {
  return request({
    url: '/app/bill-in/approve/' + id,
    method: 'post'
  })
}

export function doneBill (id) {
  return request({
    url: '/app/bill-in/done/' + id,
    method: 'post'
  })
}

export function cancelBill (id) {
  return request({
    url: '/app/bill-in/cancel/' + id,
    method: 'post'
  })
}

export function deleteBill (id) {
  return request({
    url: '/bill-in/delete/' + id,
    method: 'post'
  })
}

export function deleteBillOut (id) {
  return request({
    url: '/bill-out/delete/' + id,
    method: 'post'
  })
}

export function getBill (id) {
  return request({
    url: '/bill-in/get/' + id,
    method: 'get'
  })
}

export function getListBillOut (index, warehouse, type = 'all') {
  return request({
    url: 'bill-out/all?page=' + index + '&warehouse_id=' + warehouse + '&type=' + type,
    method: 'get'
  })
}

export function getBillOut (id) {
  return request({
    url: '/bill-out/get/' + id,
    method: 'get'
  })
}

export function editDetailBill (data) {
  return request({
    url: '/bill-out/change',
    method: 'post',
    data: data
  })
}

export function deleteProductInBillOut (id) {
  return request({
    url: '/bill-out/product/delete/' + id,
    method: 'post'
  })
}


export function deleteProductInBillIn (id) {
  return request({
    url: '/bill-in/product/delete/' + id,
    method: 'post'
  })
}

export function saveNoteBillIn (id, data) {
  return request({
    url: '/bill-in/note/' + id,
    method: 'put',
    data: {
      note: data
    }
  })
}

export function saveNoteBillOut (id, data) {
  return request({
    url: '/bill-out/note/' + id,
    method: 'put',
    data: {
      note: data
    }
  })
}

export function loadSettingBill () {
  return request({
    url: 'app/load-setting-bill',
    method: 'get'
  })
}

export function saveSettingBill (data) {
  return request({
    url: '/app/save-setting-bill',
    method: 'put',
    data: data
  })
}

export function getPrintBill (id) {
  return request({
    url: 'app/get-print-bill/' + id,
    method: 'get'
  })
}



