import request from '@/utils/request'

export function getListWarehouse () {
  return request({
    url: '/app/warehouse/list',
    method: 'get'
  })
}

export function createWarehouse (data) {
  return request({
    url: '/warehouse',
    method: 'post',
    data: data
  })
}

export function loadWarehouse (isPagi = null, index = null) {
  let query = ''
  if (isPagi) {
    query += '?is_pagi=true'
    if (index) {
      query += '&page=' + index
    }
  }
  return request({
    url: '/warehouse' + query,
    method: 'get'
  })
}

export function switchWarehouse (idProperty, idWarehouse, count) {
  return request({
    url: '/app/warehouse/switch',
    method: 'post',
    data: {
      property_id: idProperty,
      warehouse_id: idWarehouse,
      count: count
    }
  })
}

export function getWarehouse (id) {
  return request({
    url: 'warehouse/' + id,
    method: 'get'
  })
}

export function editWarehouse (id, data) {
  return request({
    url: 'warehouse/' + id,
    method: 'put',
    data: data
  })
}



