<template>
<div class="row">
  <div class="col-lg-6 col-md-12">
    <div class="card">
      <div class="card-header">
        <h4 class="card-title">{{ $t('app.supplier.add') }}</h4>
      </div>
      <div class="card-content">
        <div class="row" v-loading="isLoading">
          <div class="col-md-12">
            <form @submit.prevent="submitSupplier">
              <div class="form-group">
                <label>{{ $t('app.supplier.name') }}</label>
                <input type="text" :placeholder="$t('app.supplier.name')" name="name" class="form-control" required>
              </div>
              <div class="form-group">
                <label>{{ $t('app.supplier.address') }}</label>
                <input type="text" :placeholder="$t('app.supplier.address')" name="address" class="form-control">
              </div>
              <div class="form-group">
                <label>{{ $t('app.supplier.phone') }}</label>
                <input type="text" :placeholder="$t('app.supplier.phone')" name="phone" class="form-control">
              </div>
              <div class="form-group">
                <label>{{ $t('app.description') }}</label>
                <textarea name="description" class="form-control" rows="4"></textarea>
              </div>
              <div class="form-group">
                <label>{{ $t('app.status') }}</label>
                <br>
                <p-switch v-model="switches.isActiveSupplier">
                  <i class="fa fa-check" slot="on"></i>
                  <i class="fa fa-close" slot="off"></i>
                </p-switch>
              </div>
              <div class="float-right">
                <!-- HIDDEN -->
                <input type="hidden" name="status" :value="switches.isActiveSupplier ? 'active' : 'lock'">
                <button type="submit" class="btn btn-fill btn-sm btn-success">{{ $t('app.save') }}</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
  import PSwitch from 'src/components/UIComponents/Switch.vue'
  import {createSupplier} from '@/api/supplier'
  export default {
    components: {
      PSwitch
    },
    data () {
      return {
        switches: {
          isActiveSupplier: true
        },
        isLoading: false
      }
    },
    methods: {
      submitSupplier (e) {
        this.isLoading = true
        let formData = new FormData(e.target)
        let form = e.target
        createSupplier(formData).then(e => {
          if (e.data.status === 'success') {
            form.reset()
          }
          this.isLoading = false
        })
      }
    }
  }
</script>



