import request from '@/utils/request'

export function getListSaleOf () {
  return request({
    url: '/sale-of/all',
    method: 'get'
  })
}

export function deleteSaleOf (id) {
  return request({
    url: '/sale-of/' + id,
    method: 'delete'
  })
}

export function getSaleOf (id) {

}

export function createSaleOf (data) {
  return request({
    url: '/sale-of/',
    method: 'post',
    data: data
  })
}

export function editSaleOf (id, data) {
  return request({
    url: '/sale-of/' + id,
    method: 'put',
    data: data
  })
}

export function loadListSaleOf () {
  return request({
    url: '/app/sale-of/load',
    method: 'get'
  })
}



