<template>
  <div class="row">
    <div class="col-md-12 col-lg-6">
      <div class="card">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.user.edit') }}</h4>
        </div>
        <div class="card-content" v-loading="isLoading">
          <form v-if="'name' in user" @submit.prevent="editUser">
            <div class="form-group">
              <label>{{ $t('app.user.name') }}</label>
              <input type="text" :placeholder="$t('app.user.name')" :value="user.name" name="name" class="form-control" required>
            </div>
            <div class="form-group">
              <label>{{ $t('app.user.username') }}</label>
              <input type="text" :placeholder="$t('app.user.username')" :value="user.username" name="username" class="form-control" required>
            </div>
            <div class="form-group">
              <label>{{ $t('app.user.status') }}</label>
                <br>
                <el-switch v-model="statusUser"
                active-color="#13ce66"
                inactive-color="#ff4949" @change="statusChange">
                </el-switch>
            </div>
            <div class="form-group">
              <label>{{ $t('app.user.roles') }}</label>
              <select name="roles" class="form-control" v-model="user.role_id">
                <template v-for="role in roles">
                  <option :value="role.id">{{ role.name }}</option>
                </template>
              </select>
            </div>
            <div class="form-group">
              <label>{{ $t('app.user.warehouse') }}</label>
              <br>
              <el-select v-model="user.listwarehouse" multiple placeholder="Select" size="large" style="width: 100%;">
                <el-option
                  v-for="item in listWarehouse"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </div>
            <div class="float-right" style="margin-top: 20px;">
              <button type="submit" class="btn btn-fill btn-square btn-warning btn-wd btn-icon"><i class="fa fa-key"></i> {{ $t('app.user.change_password') }}</button>
              <button type="submit" class="btn btn-fill btn-square btn-success btn-wd">{{ $t('app.save') }}</button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {loadUser, editUser} from '@/api/user'
  import {loadListPermission} from '@/api/permission'
  import {getListWarehouse} from '@/api/warehouse'
  export default {
    data () {
      return {
        user: {},
        isLoading: false,
        roles: [],
        statusUser: false,
        listWarehouse: []
      }
    },
    methods: {
      loadUser (id) {
        this.isLoading = true
        loadUser(id).then(e => {
          if (e.data.is_valid) {
            this.user = e.data.data
            if (this.user.roles.super_admin === 1) {
              this.$router.push('/')
            }
            if (this.user.status === 'active') {
              this.statusUser = true
            }
            if (this.user.roles.retail === 1) {
              this.getListWarehouse()
            }
          }
          this.isLoading = false
        })
      },
      editUser () {
        this.isLoading = true
        let id = this.user.id
        editUser(id, this.user).then(e => {
          this.isLoading = false
        })
      },
      statusChange (value) {
        if (value) {
          this.user.status = 'active'
        } else {
          this.user.status = 'unactive'
        }
      },
      getListWarehouse () {
        getListWarehouse().then(e => {
          this.listWarehouse = e.data.warehouse.filter(e => {
            return e.name !== 'root'
          })
          this.user.listwarehouse = []
          if (this.user.warehouses.length >= 1) {
            this.user.warehouses.forEach(e => {
              this.user.listwarehouse.push(e.id)
            })
          }
        })
      }
    },
    mounted () {
      this.loadUser(this.$route.params.id)
      loadListPermission().then(e => {
        this.roles = e.data.data.filter(ee => {
          return ee.super_admin !== 1
        })
      })
    }
  }
</script>



