<template>
  <div class="row">
    <div class="col-md-12 col-lg-6">
      <div class="card" v-loading="isLoading">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.package.input') }}</h4>
          <p><strong>{{ package.name }}</strong></p>
        </div>
        <div class="row card-content">
          <div class="col-md-12">
            <form @submit.prevent="existProduct">
              <label>{{ $t('app.product.select') }}</label>
              <div class="form-group input-group">
                  <input type="input" class="form-control" :value="inputSelectProduct" :placeholder="$t('app.product.select')" disabled>
                  <span class="input-group-btn">
                    <button class="btn btn-fill btn-danger" type="button" @click="showDialog()">Chọn</button>
                  </span>
              </div>
              <div class="row u-custom-properties" v-for="(index, item) in countProperty">
                <div class="col-xs-4 col-sm-5 col-md-5">
                  <div class="form-group">
                    <label>{{ $t('app.product.color') }}</label>
                    <input type="text" :placeholder="$t('app.product.color')" name="list_color[]" class="form-control" required>
                  </div>
                </div>
                <div class="col-xs-4 col-sm-5 col-md-4">
                  <div class="form-group">
                    <label>{{ $t('app.product.size') }}</label>
                    <input type="text" :placeholder="$t('app.product.size')" name="list_size[]" class="form-control" required>
                  </div>
                </div>
                <div class="col-xs-4 col-sm-2 col-md-3">
                  <div class="form-group">
                    <label>{{ $t('app.count') }}</label>
                    <input type="number" :placeholder="$t('app.count')" name="list_count[]" class="form-control" required>
                  </div>
                </div>
                <div class="col-xs-12">
                  <div class="form-group">
                    <label>{{ $t('app.status') }}</label>
                    <select class="form-control" name="status[]" v-model="statusPackage[index-1]" required>
                      <option value="ready">{{ $t('app.package.status.ready') }}</option>
                      <option value="wash">{{ $t('app.package.status.wash') }}</option>
                      <option value="delete">{{ $t('app.package.status.delete') }}</option>
                    </select>
                  </div>
                </div>
                <div class="col-xs-12" v-if="statusPackage[index-1] === 'ready'">
                  <div class="form-group">
                    <label>{{ $t('app.package.warehouse') }}</label>
                    <select name="warehouse[]" class="form-control">
                      <template v-for="warehouse in listWarehouse">
                        <option :value="warehouse.id">
                            <span v-if="warehouse.name === 'root'">{{ $t('app.warehouse.root') }}</span>
                            <span v-else>{{ warehouse.name }}</span>
                        </option>
                      </template>
                    </select>
                  </div>
                </div>
                <template v-else>
                  <input type="hidden" name="warehouse[]" value="0">
                </template>
              </div>
              <div>
                <button type="button" class="btn btn-wd btn-success btn-sm" @click.prevent="++countProperty">
                  <span class="btn-label"><i class="fa fa-plus"></i></span>
                  {{ $t('app.product.add_property') }}
                </button>
                <button type="button" class="btn btn-wd btn-danger btn-sm" :disabled="countProperty === 0" @click.prevent="--countProperty">
                  <span class="btn-label"><i class="fa fa-close"></i></span>
                  {{ $t('app.product.delete_property') }}
                </button>
              </div>
              <div class="float-right">
                  <input type="hidden" name="action" value="exist">
                  <input type="hidden" name="type" value="item">
                  <input type="hidden" name="product_id" :value="idSelectProduct">
                <button type="submit" class="btn btn-fill btn-wd btn-sm btn-success" :disabled="idSelectProduct === null || countProperty <= 0">{{ $t('app.save') }}</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>

    <div class="col-md-12 col-lg-6">
      <div class="card" v-loading="isLoadingListItem">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.package.list_item') }}</h4>
        </div>
        <div class="card-content">
          <el-table :data="listProductItem" show-summary :summary-method="getSummaries">
            <el-table-column type="index">

            </el-table-column>
            <el-table-column prop="product.name" min-width="150"
                             :label="$t('app.product.name')">
              <template slot-scope="props">
                <span :title="$t('app.product.size') + ': ' + props.row.size_of + ' - ' + $t('app.product.color') + ': ' + props.row.color_of">{{ props.row.product.name }}</span>
              </template>
            </el-table-column>
            <el-table-column prop="count" min-width="100"
                             :label="$t('app.count')">
            </el-table-column>
            <el-table-column min-width="150"
                             :label="$t('app.product.price_out')">
              <template slot-scope="props">
                <span>{{ $price(props.row.product.price) + $t('app.price_suffix') }}</span>
              </template>
            </el-table-column>
            <el-table-column min-width="150"
                             :label="$t('app.status')">
              <template slot-scope="props">
                <template v-if="props.row.status === 'ready'">
                  <span class="label label-success">{{ $t('app.package.status.ready') }}</span>
                </template>
                <template v-if="props.row.status === 'wash'">
                  <span class="label label-warning">{{ $t('app.package.status.wash') }}</span>
                </template>
                <template v-if="props.row.status === 'delete'">
                  <span class="label label-danger">{{ $t('app.package.status.delete') }}</span>
                </template>
              </template>
            </el-table-column>
            <el-table-column :label="$t('app.product.action')" min-width="100">
              <template slot-scope="props">
                <a class="btn btn-danger btn-sm btn-icon btn-fill btn-square"  @click="handleDelete(props.$index, props.row)"><i class="fa fa-times"></i></a>
              </template>
            </el-table-column>
          </el-table>
          <div class="float-right" style="margin-top: 10px;">
              <!-- <button class="btn btn-warning btn-sm btn-fill btn-wd">{{ $t('app.cancel') }}</button> -->
              <!-- <button class="btn btn-success btn-sm btn-fill btn-wd" @click="donePackage">{{ $t('app.save') }}</button> -->
          </div>
        </div>
      </div>
    </div>


    <el-dialog
      :title="dialog.title"
      :visible.sync="dialogVisible"
      width="40%">
      <div class="row">
        <div class="col-xs-12">
          <div class="form-group">
            <label>{{ dialog.titleSearch }}</label>
            <input type="text" @input="inputSearch" v-model="searchString" class="form-control"
            :placeholder="dialog.titleSearch">
            <div style="max-height: 400px; overflow: auto;" v-loading="loadingTable">
              <el-table
                ref="singleTable"
                :data="listData"
                highlight-current-row
                @current-change="handleCurrentChange"
                style="width: 100%">
                <el-table-column
                  type="index"
                  width="100">
                </el-table-column>
                <el-table-column property="string_id" :label="$t('app.product.string_id')">
                </el-table-column>
                <el-table-column
                  property="name"
                  :label="$t('app.product.name')"
                  min-width="100">
                </el-table-column>
                <el-table-column property="product_count" label="Count" min-width="80">

                </el-table-column>
              </el-table>
            </div>
          </div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <div @click="dialogVisible = false" class="btn btn-sm btn-primary">{{ $t('app.cancel') }}</div>
        <div @click="confirmSelected" class="btn btn-sm btn-danger" :disabled="currentSelected === null">{{ $t('app.confirm') }}</div>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import swal from 'sweetalert'
  import {loadListPropertiesProduct, searchProduct, inputFormPackage, deleteProperty} from '@/api/product'
  import {getListWarehouse} from '@/api/warehouse'
  export default {
    data () {
      return {
        package: {},
        isLoading: false,
        countProperty: 1,
        inputSelectProduct: '',
        idSelectProduct: null,
        dialog: {
          title: 'Ahihi',
          titleSearch: 'ahihi'
        },
        dialogVisible: false,
        loadingTable: false,
        searchString: '',
        listData: [],
        currentSelected: null,
        statusPackage: [],
        listWarehouse: [],
        listProductItem: [],
        isLoadingListItem: false
      }
    },
    mounted () {
      this.loadListPackage()
      getListWarehouse().then(e => {
        if (e.data.is_valid) {
          this.listWarehouse = e.data.warehouse
        }
      })
    },
    methods: {
      inputSearch (e) {
        let value = e.target.value
        setTimeout(() => {
          if (this.searchString === value) {
            this.loadingTable = true
            searchProduct(value).then(e => {
              this.listData = e.data.data
              this.loadingTable = false
            })
          }
        }, 500)
      },
      handleCurrentChange (row) {
        this.currentSelected = row
      },
      confirmSelected () {
        this.idSelectProduct = this.currentSelected.id
        this.inputSelectProduct = this.currentSelected.name
        this.dialogVisible = false
      },
      showDialog () {
        this.dialog.title = this.$t('app.routes.product')
        this.dialog.titleSearch = this.$t('app.title_search', {name: this.$t('app.routes.product')})
        this.dialogVisible = true
      },
      existProduct (e) {
        let formData = new FormData(e.target)
        let form = e.target
        let id = this.package.id
        inputFormPackage(id, formData).then(e => {
          if (e.data.status === 'success') {
            form.reset()
            this.loadListPackage()
          }
        })
      },
      getSummaries (param) {
        let sums = []
        const {data} = param
        sums[1] = this.$t('app.sum')
        let sumCount = 0
        data.forEach(e => {
          sumCount += e.count
        })
        sums[2] = sumCount
        return sums
      },
      donePackage () {

      },
      loadListPackage () {
        this.isLoading = true
        loadListPropertiesProduct(this.$route.params.id).then(e => { // note here
          if (e.data.is_valid) {
            this.package = e.data.data
            this.listProductItem = e.data.data.package_item
          }
          this.isLoading = false
        })
      },
      handleDelete (index, row) {
        swal({
          text: this.$t('app.product.delete_more_bill'),
          icon: 'warning',
          buttons: true,
          dangerMode: true
        }).then(isDelete => {
          this.isLoading = true
          if (isDelete) {
            this.isListProductLoading = true
            deleteProperty(row.id).then(e => {
              if (e.data.status === 'success') {
                this.loadListPackage()
              }
              this.isLoading = false
            })
          }
        })
      }
    }
  }
</script>

<style scoped>
  form {
    text-align: left;
  }
  .u-custom-properties {
    padding: 10px;
  }
</style>



