import request from '@/utils/request'

export function searchCustomer (name, type = 'retail') {
  return request({
    url: '/app/customer/search?name=' + name + '&type=' + type,
    method: 'GET'
  })
}

export function createCustomer (data) {
  return request({
    url: '/customer',
    method: 'POST',
    data: data
  })
}

export function getListCustomer (index) {
  return request({
    url: '/customer?page=' + index,
    method: 'get'
  })
}

export function deleteCustomer (id) {
  return request({
    url: '/customer/' + id,
    method: 'delete'
  })
}


export function getCustomer (id) {
  return request({
    url: '/customer/' + id,
    method: 'get'
  })
}

export function editCustomer (id, data) {
  return request({
    url: '/customer/' + id,
    method: 'put',
    data: data
  })
}



