<template>
  <div class="row">
    <div class="col-md-6">
      <div class="card" v-loading="isLoading">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.bill.setting') }}</h4>
        </div>
        <div class="card-content">
          <p-checkbox v-model="bill.is_header">{{ $t('app.bill.header_bill') }}</p-checkbox>
          <template v-if="bill.is_header">
            <textarea v-model="bill.text_header" rows="5" class="form-control"></textarea>
          </template>
          <p-checkbox v-model="bill.is_footer">{{ $t('app.bill.footer_bill') }}</p-checkbox>
          <template v-if="bill.is_footer">
            <textarea v-model="bill.text_footer" rows="5" class="form-control"></textarea>
          </template>
          <div class="float-right">
            <button v-if="bill.is_footer || bill.is_header" class="btn btn-fill btn-sm btn-success" @click="saveSettingBill()">{{ $t('app.save') }}</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
  import {loadSettingBill, saveSettingBill} from '@/api/bill'
  export default {
    data () {
      return {
        bill: {
          is_header: false,
          is_footer: false,
          text_header: '',
          text_footer: ''
        },
        isLoading: false
      }
    },
    methods: {
      loadSettingBill () {
        this.isLoading = true
        loadSettingBill().then(e => {
          if (e.data.is_valid) {
            if (e.data.header.length >= 1) {
              this.bill.is_header = true
              this.bill.text_header = e.data.header
            }
            if (e.data.footer.length >= 1) {
              this.bill.is_footer = true
              this.bill.text_footer = e.data.footer
            }
          }
          this.isLoading = false
        })
      },
      saveSettingBill () {
        this.isLoading = true
        saveSettingBill(this.bill).then(e => {
          if (e.data.status === 'success') {
            this.loadSettingBill()
          }
          this.isLoading = false
        })
      }
    },
    mounted () {
      this.loadSettingBill()
    }
  }
</script>



