<template>
  <div class="row">
    <div class="col-md-12 col-lg-6">
      <div class="card">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.user.new') }}</h4>
        </div>
        <div class="card-content" v-loading="isLoading">
          <form @submit.prevent="addUser">
            <div class="form-group">
              <label>{{ $t('app.user.name') }}</label>
              <input type="text" :placeholder="$t('app.user.name')" name="name" class="form-control" required>
            </div>
            <div class="form-group">
              <label>{{ $t('app.user.username') }}</label>
              <input type="text" :placeholder="$t('app.user.username')" name="username" class="form-control" required>
            </div>
            <div class="form-group">
              <label>{{ $t('app.user.status') }}</label>
                <br>
                <el-switch v-model="statusUser"
                active-color="#13ce66"
                inactive-color="#ff4949">
                </el-switch>
            </div>
            <div class="form-group">
              <label>{{ $t('app.user.roles') }}</label>
              <select name="roles" class="form-control" v-model="rolesId">
                <template v-for="role in roles">
                  <option :value="role.id">{{ role.name }}</option>
                </template>
              </select>
            </div>
            <div class="form-group">
              <label>{{ $t('app.user.password') }}</label>
              <input type="password" :placeholder="$t('app.user.password')" name="password" class="form-control" required ref="password">
            </div>
            <div class="form-group">
              <label>{{ $t('app.user.repassword') }}</label>
              <input type="password" v-validate="'required|confirmed:password'" :placeholder="$t('app.user.repassword')" name="repassword" class="form-control" required>
            </div>
            <div class="float-right" style="margin-top: 20px;">
              <input type="hidden" name="role_id" :value="rolesId">
              <input type="hidden" name="status" :value="statusUser ? 'active' : 'unactive'">
              <button type="submit" class="btn btn-fill btn-square btn-success btn-wd">{{ $t('app.save') }}</button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {addUser} from '@/api/user'
  import {loadListPermission} from '@/api/permission'
  export default {
    data () {
      return {
        isLoading: false,
        roles: [],
        statusUser: true,
        rolesId: null
      }
    },
    methods: {
      addUser (e) {
        let formData = new FormData(e.target)
        let form = e.target
        this.isLoading = true
        addUser(formData).then(e => {
          if (e.data.status === 'success') {
            form.reset()
          }
          this.isLoading = false
        })
      }
    },
    mounted () {
      loadListPermission().then(e => {
        this.roles = e.data.data.filter(ee => {
          return ee.super_admin !== 1
        })
      })
    }
  }
</script>



