<template>
<div class="row">
  <div class="col-lg-6 col-md-12">
    <div class="card">
      <div class="card-header">
        <h4 class="card-title">{{ $t('app.spend.add') }}</h4>
      </div>
      <div class="card-content">
        <div class="row" v-loading="isLoading">
          <div class="col-md-12">
            <form @submit.prevent="submit">
              <div class="form-group">
                <label>{{ $t('app.spend.price') }}</label>
                <money class="form-control" v-model="price" required></money>
              </div>
              <div class="form-group">
                <label>{{ $t('app.spend.description') }}</label>
                <textarea name="description" class="form-control" rows="4" required></textarea>
              </div>
              <div class="float-right">
                <input type="hidden" name="user_id" :value="user.id">
                <input type="hidden" name="price" :value="price">
                <button type="submit" class="btn btn-fill btn-sm btn-success">{{ $t('app.save') }}</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
  import {createSpend} from '@/api/spend'
  export default {
    data () {
      return {
        isLoading: false,
        user: this.$store.getters.user,
        price: 0
      }
    },
    methods: {
      submit (e) {
        this.isLoading = true
        let formData = new FormData(e.target)
        let form = e.target
        createSpend(formData).then(e => {
          if (e.data.status === 'success') {
            form.reset()
          }
          this.isLoading = false
        })
      }
    }
  }
</script>



