<template>
  <div>
    <div class="row" v-if="selectedWarehouse !== 1">
      <div class="col-lg-5 col-md-12">
        <div class="card">
          <div class="card-content">
            <div class="float-right">
              <button class="btn btn-primary btn-icon btn-square btn-fill" v-if="laterBill !== 0" @click="printBill"><i class="fa fa-print"></i></button>
            </div>
            <div class="u-search-product">
              <label>{{ $t('app.sale.customer') }}</label>
              <div class="form-group input-group">
                  <input type="input" class="form-control" :value="inputSelectCustomer" disabled>
                  <span class="input-group-btn">
                    <button class="btn btn-fill btn-danger" type="button" @click="showDialog('customer')">{{ $t('app.sale.select') }}</button>
                    <button class="btn btn-fill btn-success" type="button" @click="showDialog('new_customer')"><i class="fa fa-plus"></i></button>
                    <button class="btn btn-fill btn-primary" type="button" :disabled="idSelectCustomer === 1" @click="refreshSelectCustomer"><i class="fa fa-refresh"></i></button>
                  </span>
              </div>
              <label>{{ $t('app.sale.search_product') }}</label>
              <div class="input-group">
                <input type="text" class="form-control" v-model="searchString" @input="productSearch" :placeholder="$t('app.sale.search_product')">
                <span class="input-group-btn">
                  <button class="btn btn-primary btn-fill btn-sm" type="button"><i class="fa fa-search"></i></button>
                  <button class="btn btn-warning btn-fill btn-sm" type="button"><i class="fa fa-barcode"></i></button>
                </span>
              </div>
            </div>
            <el-table :data="listProductSearch" v-loading="isLoadingSearch">
              <el-table-column label="#" prop="string_id" min-width="100">

              </el-table-column>
              <el-table-column prop="name"
                               :label="$t('app.product.name')"
                               min-width="200">
              </el-table-column>
              <el-table-column :label="$t('app.product.price_out')"
                               min-width="200">
                <template slot-scope="props">
                  <span>{{ $price(props.row.price) + $t('app.price_suffix') }}</span>
                </template>
              </el-table-column>
              <el-table-column min-width="50">
                <template slot-scope="props">
                  <button class="btn btn-sm btn-icon btn-success btn-square btn-fill" @click="btnAddProduct(props.row, props.$index)"><i class="fa fa-arrow-right"></i></button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
      </div>
      <div class="col-lg-7 col-md-12">
        <div class="card">
          <div class="card-header">
            <h4 class="card-title">{{ $t('app.sale.list_added') }}</h4>
          </div>
          <div class="card-content">
            <el-table :data="listAdded" v-loading="isLoadingAdded" show-summary :summary-method="getSummaries">
              <el-table-column type="index" label="#" min-width="100">

              </el-table-column>
              <el-table-column :label="$t('app.product.name')" min-width="150">
                <template slot-scope="props">
                  <span v-if="props.row.is_sale_of" class="has-sale-of" :title="props.row.name_of_sale_of + ' ' + props.row.percent + ' %'">{{ props.row.name }}</span>
                  <span v-else>{{ props.row.name }}</span>
                </template>
              </el-table-column>
              <el-table-column :label="$t('app.product.size_color')" min-width="150">
                <template slot-scope="props">
                  <template v-if="props.row.property.length >= 1">
                    <el-select v-model="props.row.property_id">
                      <el-option
                          v-for="item in props.row.property"
                          :key="item.id"
                          :label="item.size_color"
                          :value="item.id">
                      </el-option>
                    </el-select>
                  </template>
                </template>
              </el-table-column>
              <el-table-column :label="$t('app.count')" min-width="140">
                <template slot-scope="props">
                  <el-input-number size="small" :min="1" v-model="props.row.mCount"></el-input-number>
                </template>
              </el-table-column>
              <el-table-column :label="$t('app.product.price_out')" min-width="150">
                <template slot-scope="props">
                  <div class="input-group input-group-sm" style="margin: 0;">
                    <money v-model="props.row.price" :class="{'form-control': true, 'has-sale-of': props.row.is_sale_of}"></money>
                  </div>
                </template>
              </el-table-column>
              <el-table-column :label="$t('app.product.price')" min-width="100">
                <template slot-scope="props">
                  <span>{{ $price(props.row.price * props.row.mCount) + $t('app.price_suffix') }}</span>
                </template>
              </el-table-column>
              <el-table-column min-width="50">
                <template slot-scope="props">
                  <button class="btn btn-sm btn-danger btn-icon btn-square btn-fill" @click="removeProduct(props.row, props.$index)"><i class="fa fa-times"></i></button>
                </template>
              </el-table-column>
            </el-table>
            <div class="detail-bill" v-if="listAdded.length >= 1" style="margin-top: 15px;" v-loading="isLoadingAdded">
              <div class="row">
                <div class="col-xs-6">
                  <!-- START APPLY SALE OF -->
                  <div class="apply-sale-of">
                    <p-checkbox v-model="isApplySaleOf" @input="chkApplySaleOf">{{ $t('app.sale.apply_sale_of') }}</p-checkbox>
                    <div class="wrapper-sale-of" v-if="isApplySaleOf">
                      <hr>
                      <p-checkbox @input="chkSaleOfType" v-model="saleOfData.is_sale_type">{{ $t('app.sale.product_type_bill_sale_of') }}</p-checkbox>
                      <p-checkbox v-model="saleOfData.is_sale">{{ $t('app.sale.sum_bill_sale_of') }}</p-checkbox>
                      <hr>
                      <div class="child-of-sum-bill" v-if="saleOfData.is_sale">
                        <template v-for="sale in listSaleOf">
                          <template v-if="sale.type === 'for_all'">
                            <p-radio v-model="saleOfData.selected_sale_of" @input="selectedSaleOfPercent(sale.percent)" :label="sale.id">{{ sale.name + ' - ' + sale.percent + ' %' }}</p-radio>
                          </template>
                        </template>
                        <hr>
                      </div>
                    </div>
                  </div>
                  <!-- END APPLY SALE OF -->
                  <div class="apply-price-off">
                    <p-checkbox v-model="priceOf.is_price_of">{{ $t('app.sale.apply_price_of') }}</p-checkbox>
                    <money v-if="priceOf.is_price_of" v-model="priceOf.money" class="form-control"></money>
                  </div>
                  <div class="wrapper-sale-of">
                    <p-checkbox v-model="note.is_note">{{ $t('app.bill.note') }}</p-checkbox>
                    <template v-if="note.is_note">
                      <textarea v-model="note.content" rows="3" :placeholder="$t('app.bill.input_note')" class="form-control"></textarea>
                    </template>
                  </div>
                </div>
                <div class="col-xs-6" style="margin-top: 10px;">
                  <!-- DETAIL PRICE -->
                  <p>{{ $t('app.bill.sum_price') }}: <strong>{{ $price(sumPrice) + $t('app.price_suffix') }}</strong></p>
                  <div class="form-group">
                    <money v-model="moneyCustomer" :placeholder="$t('app.bill.money_cutomer')" class="form-control"></money>
                  </div>
                  <p>{{ $t('app.bill.excess_cash') }}: <strong>{{ $price(moneyCustomer - sumPrice) + $t('app.price_suffix') }}</strong></p>
                </div>
              </div>
              <div class="float-right">
                <button class="btn btn-fill btn-square btn-danger" @click="newSale">{{ $t('app.bill.new') }}</button>
                <button class="btn btn-fill btn-square btn-success" :disabled="!formValid" @click="save">{{ $t('app.save') }}</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="row" v-else>
      <div class="col-md-12">
        <h5>{{ $t('app.sale.retail_not_avaliable') }}</h5>
      </div>
    </div>
    <!-- DIALOG HERE -->
    <el-dialog
      :title="$t('app.sale.dialog.select_cusomers')"
      :visible.sync="dialogVisibleCustomers"
      width="40%">
      <div class="row">
        <div class="col-xs-12">
          <div class="form-group">
            <label>{{ $t('app.sale.dialog.search_customer') }}</label>
            <input type="text" @input="inputSearchCustomers" v-model="searchStringCustomers" class="form-control"
            :placeholder="$t('app.sale.dialog.search_customer')">
            <div style="max-height: 400px; overflow: auto;" v-loading="loadingTable">
              <el-table
                ref="singleTable"
                :data="listData"
                highlight-current-row
                @current-change="handleCurrentChange"
                style="width: 100%">
                <el-table-column
                  type="index"
                  width="100">
                </el-table-column>
                <el-table-column prop="name" :label="$t('app.customer.name')">

                </el-table-column>
                <el-table-column prop="phone" :label="$t('app.customer.phone')">

                </el-table-column>
                <el-table-column prop="visited_count" :label="$t('app.customer.visit_count')">

                </el-table-column>
              </el-table>
            </div>
          </div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <div @click="dialogVisibleCustomers = false" class="btn btn-sm btn-primary">{{ $t('app.cancel') }}</div>
        <div @click="confirmSelected" class="btn btn-sm btn-danger" :disabled="currentSelected === null">{{ $t('app.confirm') }}</div>
      </span>
    </el-dialog>
    <!-- DIALOG NEW CUSTOMER -->
    <el-dialog :title="$t('app.sale.dialog.new_cusomer')"
      :visible.sync="dialogVisibleNewCustomers"
      width="40%">
      <div class="row">
        <div class="col-xs-12" v-loading="loadingNewCustomer">
          <form @submit.prevent ref="formCreateCustomer">
            <div class="form-group">
              <label>{{ $t('app.customer.name') }}</label>
              <input v-validate="'required'" v-model="formNewCustomer.name" type="text" class="form-control" name="name" :placeholder="$t('app.customer.name')">
            </div>
            <div class="form-group">
              <label>{{ $t('app.customer.phone') }}</label>
              <input v-validate="'required'" v-model="formNewCustomer.phone" type="text" class="form-control" name="phone" :placeholder="$t('app.customer.phone')">
            </div>
            <div class="form-group">
              <label>{{ $t('app.customer.zalo') }}</label>
              <input type="text" class="form-control" name="zalo" :placeholder="$t('app.customer.zalo')">
            </div>
            <div class="form-group">
              <label>{{ $t('app.customer.facebook') }}</label>
              <input type="text" class="form-control" name="facebook" :placeholder="$t('app.customer.facebook')">
            </div>
            <div class="form-group">
              <label>{{ $t('app.customer.address') }}</label>
              <textarea name="address" rows="3" :placeholder="$t('app.customer.address')" class="form-control"></textarea>
            </div>
          </form>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <div @click="dialogVisibleNewCustomers = false" class="btn btn-sm btn-primary">{{ $t('app.cancel') }}</div>
        <div @click="confirmCreateCustomer" class="btn btn-sm btn-danger">{{ $t('app.confirm') }}</div>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import {searchProduct} from '@/api/product'
  import {loadListSaleOf} from '@/api/saleof'
  import {searchCustomer, createCustomer} from '@/api/customer'
  import {getPrintBill} from '@/api/bill'
  import {saleSaveBillRetail} from '@/api/app'
  import swal from 'sweetalert'
  export default {
    data () {
      return {
        listProductSearch: [],
        searchString: '',
        inputSelectCustomer: this.$t('app.customer.root'),
        idSelectCustomer: 1,
        isLoadingSearch: false,
        listAdded: [],
        isLoadingAdded: false,
        sumPrice: 0,
        moneyCustomer: 0,
        listWarehouse: [],
        dialogVisibleCustomers: false,
        searchStringCustomers: '',
        loadingTable: false,
        listData: [],
        currentSelected: null,
        dialogVisibleNewCustomers: false,
        loadingNewCustomer: false,
        formNewCustomer: {
          name: '',
          phone: ''
        },
        isApplySaleOf: false,
        listSaleOf: [],
        saleOfData: {
          is_sale: false,
          selected_sale_of: 0,
          is_sale_type: false,
          percent: 1
        },
        priceOf: {
          money: 0,
          is_price_of: false
        },
        note: {
          is_note: false,
          content: ''
        },
        laterBill: 0
      }
    },
    methods: {
      productSearch (e) {
        let value = e.target.value
        setTimeout(() => {
          if (value === this.searchString) {
            this.isLoadingSearch = true
            searchProduct(value, this.selectedWarehouse).then(e => {
              if (e.data.is_valid) {
                this.listProductSearch = e.data.data.filter(e => {
                  return e.property.length >= 1
                })
              }
              this.isLoadingSearch = false
            })
          }
        }, 500)
      },
      showDialog (type) {
        if (type === 'customer') {
          this.listData = []
          this.currentSelected = null
          this.dialogVisibleCustomers = true
        }
        if (type === 'new_customer') {
          this.dialogVisibleNewCustomers = true
        }
      },
      btnAddProduct (row, index) {
        if (row.property.length <= 0) {
          this.$notify({
            message: this.$t('app.product.null'),
            icon: this.$t('icon.warning'),
            type: 'warning'
          })
          return false
        }
        let checkExist = false
        this.listAdded.forEach(e => {
          if (e.id === row.id) {
            checkExist = true
          }
        })
        if (checkExist) {
          return false
        }
        row.property.forEach(e => {
          e.size_color = (e.color_of + ' - ' + e.size_of).toUpperCase()
        })
        row.mCount = 1
        row.property_id = row.property[0].id
        row.root_price = row.price
        let json = JSON.stringify(row)
        this.listAdded.push(JSON.parse(json))
      },
      removeProduct (row, index) {
        this.listAdded.splice(index, 1)
      },
      getSummaries (param) {
        let sums = []
        const {data} = param
        sums[1] = this.$t('app.sum')
        let sumCount = 0
        let sumPrice = 0
        data.forEach(e => {
          sumCount += e.mCount
          sumPrice += (e.mCount * e.price)
        })
        if (this.saleOfData.is_sale) {
          sumPrice = sumPrice - ((sumPrice / 100) * this.saleOfData.percent)
        }
        if (this.priceOf.is_price_of) {
          sumPrice -= this.priceOf.money
        }
        this.sumPrice = sumPrice
        sums[3] = sumCount
        sums[5] = this.$price(sumPrice) + this.$t('app.price_suffix')
        return sums
      },
      newSale () {
        swal({
          text: this.$t('app.bill.alert_new'),
          icon: 'warning',
          buttons: true,
          dangerMode: true
        }).then(isDelete => {
          if (isDelete) {
            this.listAdded = []
            this.resetAfterSale()
          }
        })
      },
      save () {
        this.isLoadingAdded = true
        saleSaveBillRetail(this.selectedWarehouse, this.listAdded, this.idSelectCustomer, this.saleOfData, this.priceOf, this.note).then(e => {
          if (e.data.status === 'success') {
            this.listAdded = []
            this.idSelectCustomer = 1
            this.inputSelectCustomer = this.$t('app.customer.root')
            this.resetAfterSale()
            this.laterBill = e.data.bill_id
          }
          this.isLoadingAdded = false
        })
      },
      inputSearchCustomers (e) {
        let value = e.target.value
        let getSearch
        setTimeout(() => {
          if (this.searchStringCustomers === value) {
            this.loadingTable = true
            getSearch = searchCustomer(value)
            getSearch.then(e => {
              this.listData = []
              if (e.data.is_valid) {
                this.listData = e.data.data
              }
              this.loadingTable = false
            })
          }
        }, 500)
      },
      handleCurrentChange (row) {
        this.currentSelected = row
      },
      confirmSelected () {
        this.dialogVisibleCustomers = false
        this.idSelectCustomer = this.currentSelected.id
        this.inputSelectCustomer = this.currentSelected.name
      },
      refreshSelectCustomer () {
        this.idSelectCustomer = 1
        this.inputSelectCustomer = this.$t('app.customer.root')
      },
      confirmCreateCustomer () {
        if (!this.formValid) {
          return false
        }
        let form = this.$refs.formCreateCustomer
        let formData = new FormData(form)
        this.loadingNewCustomer = true
        createCustomer(formData).then(e => {
          if (e.data.status === 'success') {
            this.idSelectCustomer = e.data.data.id
            this.inputSelectCustomer = e.data.data.name
            form.reset()
            this.dialogVisibleNewCustomers = false
          }
          this.loadingNewCustomer = false
        })
      },
      chkSaleOfType (e) {
        if (e) {
          this.listAdded.map(e => {
            this.listSaleOf.forEach(item => {
              if (item.product_type_id === e.product_type_id) {
                e.sale_of_id = item.id
                e.is_sale_of = true
                e.percent = item.percent
                e.price = (e.root_price) - ((e.root_price / 100) * item.percent)
                e.name_of_sale_of = item.name
              }
            })
          })
        } else {
          this.listAdded.map(e => {
            this.listSaleOf.forEach(item => {
              if (item.product_type_id === e.product_type_id) {
                delete e.sale_of_id
                delete e.is_sale_of
                delete e.name_of_sale_of
                delete e.percent
                e.price = e.root_price
              }
            })
          })
        }
      },
      chkApplySaleOf (e) {
        if (e) {
          this.isLoadingAdded = true
          loadListSaleOf().then(e => {
            if (e.data.is_valid) {
              this.listSaleOf = e.data.sale_of
              let flag = true
              this.listSaleOf.forEach(e => {
                if (e.type === 'for_all') {
                  if (flag) {
                    this.saleOfData.selected_sale_of = e.id
                    this.saleOfData.percent = e.percent
                    flag = false
                  }
                }
              })
            }
            this.isLoadingAdded = false
          })
        }
      },
      selectedSaleOfPercent (e) {
        this.saleOfData.percent = e
      },
      resetAfterSale () {
        this.isApplySaleOf = false
        this.saleOfData = {
          is_sale: false,
          selected_sale_of: 0,
          is_sale_type: false,
          percent: 1
        }
        this.priceOf = {
          money: 0,
          is_price_of: false
        }
        this.note = {
          is_note: false,
          content: ''
        }
      },
      printBill () {
        this.isLoading = true
        getPrintBill(this.laterBill).then(e => {
          if (e.data.is_valid) {
            this.$htmlToPaper(e.data.data)
          }
          this.isLoading = false
        })
      }
    },
    computed: {
      selectedWarehouse: function () {
        return this.$store.getters.selectedWarehouse
      },
      formValid () {
        return Object.keys(this.fields).every(field => {
          return this.fields[field] && this.fields[field].valid
        })
      }
    }
  }
</script>


<style>
  .has-sale-of {
    color: red;
  }
</style>



