<template>
  <div class="row">
    <div class="col-md-12" v-loading="isLoading">
      <div class="card">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.bill.bill_in') }}</h4>
        </div>
        <div class="card-content" v-if="bill.id">
          <div class="row">
            <div class="col-xs-6">
              <h4>#{{ bill.id }}</h4>
              <p>{{ $t('app.status') }}:
                <span v-if="bill.status === 'wait'" class="label label-warning">{{ $t('app.bill.status.wait') }}</span>
                <span v-if="bill.status === 'done'" class="label label-success">{{ $t('app.bill.status.done') }}</span>
                <span v-if="bill.status === 'wait_approve'" class="label label-danger">{{ $t('app.bill.status.wait_approve') }}</span>
              </p>
              <p>{{ $t('app.bill.user') }}: <strong>{{ bill.user.name }}</strong></p>
              <p>{{ $t('app.bill.sum_price') }} <strong>{{ sumPrice }}</strong></p>
              <p>{{ $t('app.bill.date_in') }} <strong>{{ bill.updated_at | moment('H:mm DD/MM/YYYY') }}</strong></p>

              <div class="action-group">
                <template v-if="(bill.user_id === currentUser.id || canModify) && bill.status === 'wait'">
                  <button class="btn btn-success btn-square btn-fill" @click="doneBill"><i class="fa fa-check"></i> {{ $t('app.bill.done_bill') }}</button>
                  <router-link class="btn btn-warning btn-square btn-fill" :to="{name: 'input', params: {param: bill.id}}"><i class="fa fa-arrow-right"></i> {{ $t('app.bill.continue') }}</router-link>
                </template>
                <template v-if="bill.status === 'wait_approve' && canApprove">
                  <button class="btn btn-success btn-square btn-fill" @click="approveBill"><i class="fa fa-check"></i> {{ $t('app.bill.approve') }}</button>
                </template>
                <template v-if="(canApprove || bill.user_id === currentUser.id) && bill.status === 'wait_approve'">
                  <button class="btn btn-warning btn-square btn-fill" @click="cancelBill"><i class="fa fa-ban"></i>
                    <span v-if="canApprove">{{ $t('app.bill.not_approve') }}</span>
                    <span v-else>{{ $t('app.bill.cancel_approve') }}</span>
                  </button>
                </template>
                <template v-if="(bill.status === 'wait' && bill.user_id === currentUser.id) || canModify">
                  <button class="btn btn-danger btn-square btn-fill" @click="deleteBill"><i class="fa fa-times"></i> {{ $t('app.bill.delete') }}</button>
                </template>
              </div>
            </div>
            <div class="col-xs-6">
              <h4>#{{ $t('app.bill.note') }}</h4>
              <textarea rows="4" v-model="bill.note" class="form-control" :disabled="isEditNote"></textarea>
              <div class="float-right">
                <template v-if="canModify || bill.user_id === currentUser.id">
                  <button class="btn btn-warning btn-square btn-fill" style="margin-top: 10px;" v-if="isEditNote" @click="isEditNote = false"><i class="fa fa-pencil"></i> {{ $t('app.bill.edit_note') }}</button>
                  <button class="btn btn-success btn-square btn-fill" style="margin-top: 10px;" v-else @click="saveNoteBill"><i class="fa fa-check"></i> {{ $t('app.bill.save_note') }}</button>
                </template>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card">
        <div class="card-header">
          <h4 class="card-title">{{ $t('app.bill.detail_bill') }}</h4>
        </div>
        <div class="card-content">
          <el-table :data="bill.product" show-summary :summary-method="getSummaries">
            <el-table-column type="index">

            </el-table-column>
            <el-table-column prop="product.name" min-width="200"
                             :label="$t('app.product.name')">
              <template slot-scope="props">
                <template v-if="props.row.product.type === 'package'">
                  <span class="label label-danger">{{ props.row.product.name }}</span>
                </template>
                <template v-else>
                  <span class="label label-info">{{ props.row.product.name }}</span>
                </template>
              </template>
            </el-table-column>
            <el-table-column prop="count" min-width="100"
                             :label="$t('app.count')">
            </el-table-column>
            <el-table-column prop="product.price_first" min-width="100"
                             :label="$t('app.product.price_in')">
              <template slot-scope="props">
                <span>{{ $price(props.row.product.price_first) + $t('app.price_suffix') }}</span>
              </template>
            </el-table-column>
            <el-table-column min-width="150"
                             :label="$t('app.product.price')">
              <template slot-scope="props">
                <span>{{ $price(props.row.count * props.row.product.price_first) + $t('app.price_suffix') }}</span>
              </template>
            </el-table-column>
            <el-table-column v-if="(bill.user_id === currentUser.id && bill.status === 'wait') || canModify"
              :label="$t('app.product.action')" min-width="100">
              <template slot-scope="props">
                <a class="btn btn-danger btn-xs btn-icon btn-fill btn-square btn-sm"  @click="handleDelete(props.$index, props.row)"><i class="fa fa-times"></i></a>
              </template>
            </el-table-column>
          </el-table>
          <div class="float-right" style="margin-top: 20px;">
            <!-- <button class="btn btn-info btn-square btn-fill"><i class="fa fa-print"></i> {{ $t('app.bill.print') }}</button> -->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {getBill, doneBill, approveBill, cancelBill, deleteBill, deleteProductInBillIn, saveNoteBillIn} from '@/api/bill'
  import swal from 'sweetalert'
  export default {
    data () {
      return {
        bill: {},
        isLoading: false,
        currentUser: this.$store.getters.user,
        canApprove: false,
        canModify: false,
        isEditNote: true
      }
    },
    mounted () {
      this.getBill(this.$route.params.id)
    },
    methods: {
      getBill (id) {
        this.isLoading = true
        getBill(id).then(e => {
          if (e.data.is_valid) {
            this.bill = e.data.bill
          }
          if (e.data.can_approve) {
            this.canApprove = true
          }
          if (e.data.can_modify) {
            this.canModify = true
          }
          this.isLoading = false
        })
      },
      getSummaries (param) {
        let sums = []
        const {data} = param
        sums[1] = this.$t('app.sum')
        let sumCount = 0
        let sumPrice = 0
        data.forEach(e => {
          sumCount += e.count
          sumPrice += (e.count * e.product.price_first)
        })
        sums[2] = sumCount
        sums[4] = this.$price(sumPrice) + this.$t('app.price_suffix')
        return sums
      },
      doneBill () {
        let id = this.bill.id
        swal({
          text: this.$t('app.bill.alert.done'),
          icon: 'info',
          buttons: true
        }).then(isOK => {
          if (isOK) {
            this.isLoading = true
            doneBill(id).then(e => {
              if (e.data.status === 'success') {
                this.isLoading = false
                this.getBill(id)
              }
            })
          }
        })
      },
      approveBill () {
        let id = this.bill.id
        swal({
          text: this.$t('app.bill.alert.approve'),
          icon: 'info',
          buttons: true
        }).then(isOK => {
          if (isOK) {
            this.isLoading = true
            approveBill(id).then(e => {
              if (e.data.status === 'success') {
                this.isLoading = false
                this.getBill(id)
              }
            })
          }
        })
      },
      cancelBill () {
        let id = this.bill.id
        swal({
          text: this.$t('app.bill.alert.cancel'),
          icon: 'warning',
          buttons: true
        }).then(isOK => {
          if (isOK) {
            this.isLoading = true
            cancelBill(id).then(e => {
              if (e.data.status === 'success') {
                this.isLoading = false
                this.getBill(id)
              }
            })
          }
        })
      },
      deleteBill () {
        let id = this.bill.id
        swal({
          text: this.$t('app.bill.alert.delete'),
          icon: 'warning',
          buttons: true,
          dangerMode: true
        }).then(isOK => {
          if (isOK) {
            this.isLoading = true
            deleteBill(id).then(e => {
              if (e.data.status === 'success') {
                this.getBill(id)
              }
              this.isLoading = false
            })
          }
        })
      },
      handleDelete (index, row) {
        swal({
          text: this.$t('app.bill.alert.product'),
          icon: 'warning',
          buttons: true,
          dangerMode: true
        }).then(isOK => {
          if (isOK) {
            this.isLoading = true
            deleteProductInBillIn(row.id).then(e => {
              if (e.data.status === 'success') {
                this.getBill(this.bill.id)
              }
              this.isLoading = false
            })
          }
        })
      },
      saveNoteBill () {
        this.isLoading = true
        saveNoteBillIn(this.bill.id, this.bill.note).then(e => {
          if (e.data.status === 'success') {
            this.getBill(this.bill.id)
          }
          this.isLoading = false
        })
      }
    },
    computed: {
      sumPrice () {
        let sum = 0
        this.bill.product.forEach(e => {
          if (e.is_from_package === 0) {
            sum += (e.count * e.product.price_first)
          }
        })
        return this.$price(sum) + this.$t('app.price_suffix')
      }
    }
  }
</script>



