<template>
<div class="row">
  <template>
    <div class="col-md-12">
      <div class="card" v-loading="isLoading">
        <div class="card-header">
          <h4 class="card-title">{{ productType.name === 'root' ? $t('app.supplier.root') : productType.name }}</h4>
        </div>
        <div class="card-content">
          <div class="row">
            <div class="col-md-6">
              <!-- DETAIL PRODUCT TYPE -->
              <div class="info-detail-package">
                <p>{{ $t('app.count_products') }}: <strong>{{ productCount }}</strong></p>
                <p>{{ $t('app.count_all_product') }}: <strong>{{ sumAllProduct }}</strong></p>
              </div>
            </div>
          </div>
          <el-table :data="listProduct.data">
            <el-table-column type="selection" min-width="70" width="70">

            </el-table-column>
            <el-table-column :label="$t('app.product.string_id')" min-width="110" prop="string_id">

            </el-table-column>
            <el-table-column :label="$t('app.product.name')" min-width="200">
              <template slot-scope="props">
                <template v-if="props.row.type === 'package'">
                  <span class="label label-danger" :title="props.row.name">{{ props.row.name }}</span>
                </template>
                <template v-else>
                  <span class="label label-info" :title="props.row.name">{{ props.row.name }}</span>
                </template>
                <template v-for="item in props.row.package_item">

                </template>
              </template>
            </el-table-column>
            <el-table-column :label="$t('app.product.price_out')"
                             min-width="100">
              <template slot-scope="props">
                <span>{{ $price(props.row.price) + $t('app.price_suffix') }}</span>
              </template>
            </el-table-column>
            <el-table-column :label="$t('app.status')"
                             min-width="100">
              <template slot-scope="props">
                <template v-if="props.row.status === 'ready'">
                  <span class="label label-success">{{ $t('app.product.status.ready') }}</span>
                </template>
                <template v-if="props.row.status === 'opened'">
                  <span class="label label-info">{{ $t('app.product.status.opened') }}</span>
                </template>
                <template v-if="props.row.status === 'selled'">
                  <span class="label label-danger">{{ $t('app.product.status.selled') }}</span>
                </template>
                <template v-if="props.row.status === 'locked'">
                  <span class="label label-default">{{ $t('app.product.status.locked') }}</span>
                </template>
                <template v-if="props.row.status === 'seller'">
                  <span class="label label-default">{{ $t('app.product.status.seller') }}</span>
                </template>
                <template v-if="props.row.status === 'done'">
                  <span class="label label-success">{{ $t('app.product.status.done') }}</span>
                </template>
                <template v-if="props.row.status === 'wait'">
                  <span class="label label-warning">{{ $t('app.product.status.wait') }}</span>
                </template>
              </template>
            </el-table-column>
            <el-table-column :label="$t('app.count')" :formatter="formatCount">

            </el-table-column>
            <el-table-column :label="$t('app.product.date_in')" min-width=130>
              <template slot-scope="props">
                <span>{{ props.row.created_at | moment('H:mm DD/MM/YYYY') }}</span>
              </template>
            </el-table-column>
            <el-table-column
              :label="$t('app.action')" min-width="300">
              <template slot-scope="props">
                  <router-link class="btn btn-info btn-icon btn-fill btn-square" :to="{name: 'detail_product', params: {id: props.row.id}}"><i class="fa fa-info"></i></router-link>
                  <template v-if="canModify">
                    <router-link class="btn btn-warning btn-icon btn-fill btn-square" :to="{name: 'edit_product', params: {id: props.row.id}}"><i class="fa fa-pencil-square-o"></i></router-link>
                    <a class="btn btn-danger btn-icon btn-fill btn-square" @click="handleDelete(props.$index, props.row)"><i class="fa fa-times"></i></a>
                  </template>
              </template>
            </el-table-column>
          </el-table>

          <div class="u-cus-pagination">
            <el-pagination v-if="listProduct.total > listProduct.per_page"
              background
              layout="prev, pager, next"
              :total="listProduct.total"
              :page-size="listProduct.per_page"
              :current-page="currentPage"
              @current-change="currentChange">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </template>
</div>
</template>

<script>
  import swal from 'sweetalert'
  import {getListProductByProductType, deleteProduct} from '@/api/product'
  export default {
    data () {
      return {
        listProduct: {},
        isLoading: false,
        currentPage: 1,
        canModify: false,
        productType: {},
        productCount: 0,
        sumAllProduct: 0
      }
    },
    methods: {
      getListProduct (id, index = 1) {
        this.listProduct = []
        this.isLoading = true
        getListProductByProductType(id, index).then(e => {
          if (e.data.is_valid) {
            this.listProduct = Object.assign({}, e.data.list_product)
            this.productType = e.data.product_type
            this.productCount = e.data.product_type.count_product_count
            this.sumAllProduct = e.data.sum_count
            this.listProduct.data = e.data.list_product.data.filter(e => {
              if (e.type === 'package' || e.property.length >= 1) {
                return true
              } else {
                return false
              }
            })
          }
          this.isLoading = false
          if (e.data.can_modify) {
            this.canModify = true
          } else {
            this.canModify = false
          }
        })
      },
      currentChange (index) {
        this.getListProduct(this.productType.id, index)
      },
      handleDelete (index, row) {
        let html = document.createElement('div')
        let id = row.id
        html.innerHTML = this.$t('app.product.delete_alert', {name: row.name})
        swal({
          icon: 'warning',
          buttons: true,
          dangerMode: true,
          content: html
        }).then(isOk => {
          if (isOk) {
            deleteProduct(id).then(e => {
              if (e.data.status === 'success') {
                this.getListProduct(this.productType.id, this.currentPage)
              }
            })
          }
        })
      },
      formatCount (row, column) {
        let sum = 0
        if (row.type === 'package') {
          sum = ''
        } else {
          row.property.forEach(e => {
            sum += e.count
          })
        }
        return sum
      }
    },
    mounted () {
      this.getListProduct(this.$route.params.id)
    },
    computed: {

    }
  }
</script>



