<template>
  <div class="content">

    <div class="container-fluid">
      <transition name="fade" mode="out-in">
        <!-- your content here -->
        <div>
          <div class="select-type-view-product" style="margin-bottom: 10px;" v-if="isValidSelect">
            <p-radio v-model="radioViewProduct" label="warehouse" inline>{{ $t('app.product.view_type.warehouse') }}</p-radio>
            <p-radio v-model="radioViewProduct" label="all" inline>{{ $t('app.product.view_type.all') }}</p-radio>
          </div>
          <router-view :key="reRender"></router-view>
        </div>
      </transition>
    </div>
  </div>
</template>
<script>
  export default {
    computed: {
      reRender: function () {
        return this.$store.getters.reRender
      },
      isValidSelect: function () {
        return this.$store.getters.isValidSelectTypeProduct && this.$store.getters.isRootWarehouse
      }
    },
    data () {
      return {
        radioViewProduct: 'warehouse'
      }
    },
    watch: {
      'radioViewProduct': function (e) {
        this.$store.dispatch('changeValueTypeSelect', e)
        this.$store.dispatch('changeReRender')
      }
    }
  }
</script>
<style>
  .fade-enter-active,
  .fade-leave-active {
    transition: opacity .15s
  }

  .fade-enter,
  .fade-leave-to
    /* .fade-leave-active in <2.1.8 */

  {
    opacity: 0
  }
</style>



