<template>
  <div class="col-lg-6 col-md-12">
    <div class="card" v-loading="isLoading">
      <div class="card-header">
        <h4 class="card-title">{{ supplier.name }}</h4>
      </div>
      <div class="card-content">
        <div class="row">
          <div class="col-md-12">
            <form @submit.prevent="editSupplier">
              <div class="form-group">
                <label>{{ $t('app.supplier.name') }}</label>
                <input type="text" :placeholder="$t('app.supplier.name')" name="name" v-model:value="supplier.name" class="form-control" required>
              </div>
              <div class="form-group">
                <label>{{ $t('app.supplier.address') }}</label>
                <input type="text" :placeholder="$t('app.supplier.address')" name="address" v-model:value="supplier.address" class="form-control">
              </div>
              <div class="form-group">
                <label>{{ $t('app.supplier.phone') }}</label>
                <input type="text" :placeholder="$t('app.supplier.phone')" name="phone" v-model:value="supplier.phone" class="form-control">
              </div>
              <div class="form-group">
                <label>{{ $t('app.description') }}</label>
                <textarea name="description" class="form-control" v-model="supplier.description" rows="4">{{ supplier.description }}</textarea>
              </div>
              <div class="float-right">
                <!-- HIDDEN -->
                <button type="submit" class="btn btn-fill btn-sm btn-success">{{ $t('app.save') }}</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {getSupplier, editSupplier} from '@/api/supplier'
  export default {
    data () {
      return {
        supplier: {},
        isLoading: false
      }
    },
    mounted () {
      console.log(this.idSupplier)
      let id = this.$route.params.id
      this.isLoading = true
      getSupplier(id).then(e => {
        if (e.data.is_valid) {
          this.supplier = e.data.supplier
          this.isLoading = false
        }
      })
    },
    methods: {
      editSupplier (e) {
        this.isLoading = true
        let form = e.target
        let id = this.supplier.id
        editSupplier(this.supplier, id).then(e => {
          if (e.data.status === 'success') {
            form.reset()
          }
          this.isLoading = false
        })
      }
    }
  }
</script>



